const jsonRequestHeader = {
  Accept: 'application/json',
  'Content-Type': 'application/json',
};

const catchStatuses = async (node: Response, statuses: number[]) => {
  let body = await node.json();
  return statuses.includes(node.status) ? await Promise.reject(body) : body;
};

