import React from 'react';
import styled from 'styled-components/macro';
import PropTypes from 'prop-types';
import { Typography } from '@material-ui/core';
import TextButton from './TextButton';
import { ColorPalette, materialAnimation } from '../../utils/StyleGuide';

const basePadding = 50;

const WarningBox = styled.div`
  display: inline-block;
  width: calc(100% - ${2 * basePadding}px);
  height: auto;
  margin: 0;
  padding-left: ${basePadding}px;
  padding-right: ${basePadding}px;
  box-shadow: 0 3px 16px 0 rgba(0, 0, 0, 0.24);
  background: ${ColorPalette.white};
  border-radius: 8px;
  opacity: ${props => (props.open ? 1 : 0)};
  transition: opacity ${materialAnimation};
`;

const Icon = styled.div`
  width: ${props => props.width}px;
  height: ${props => props.height}px;
  margin-top: 48px;
  margin-left: 50%;
  transform: translateX(-50%);
  background-color: ${props => props.iconcolor || ColorPalette.solidRed};
  mask-image: url(${props => props.icon});
  mask-size: ${props => props.width}px ${props => props.height}px;
  mask-repeat: no-repeat;
`;

const WarningText = styled.div`
  display: table;
  width: max-content;
  max-width: 100%;
  height: auto;
  min-height: 112px; /*98px;*/
  vertical-align: middle;
  color: ${ColorPalette.textDark};
  font-size: ${props => props.textSize}px;
  line-height: 1.89;
  text-align: ${props => props.textAlign};
  margin-top: 20px;
  padding-bottom: ${props => (props.noBottomPadding ? 0 : 23)}px;
  white-space: pre-wrap;
  margin-left: 50%;
  transform: translateX(-50%);
`;

const CenteredText = styled.div`
  display: table-cell;
  vertical-align: middle;
`;

const TitleText = styled(({ ...props }) => <Typography {...props} />)`
  width: 100%;
  height: auto;
  color: ${props => props.iconcolor};
  font-size: 28px;
  font-weight: 600;
  line-height: 32px;
  text-align: center;
  margin-top: 12px;
`;

const ButtonsContainer = styled.div`
  padding-left: 90px;
  padding-right: 90px;
  height: 40px;
`;

const Spacer = styled.div`
  width: 100%;
  height: 50px;
  display: block;
`;

const Warning = ({
  buttons,
  open,
  icon,
  iconSize,
  iconColor,
  text,
  textSize,
  textAlign,
  title,
  targetBounds,
  hideWarningAction,
}) => (
  <WarningBox open={open} bounds={targetBounds}>
    <Icon
      icon={icon}
      iconcolor={iconColor}
      width={iconSize.width}
      height={iconSize.height}
    />
    {!!title ? <TitleText iconcolor={iconColor}>{title}</TitleText> : null}
    {!!text ? (
      <WarningText
        noBottomPadding={!buttons.length}
        textSize={textSize}
        textAlign={textAlign}
      >
        <CenteredText>{text}</CenteredText>
      </WarningText>
    ) : null}
    {!!buttons.length ? (
      <ButtonsContainer>
        {buttons.map(button => (
          <TextButton
            {...button}
            buttonCallback={() => {
              if (hideWarningAction) {
                hideWarningAction();
              }
              button.buttonCallback(button.name);
            }}
            key={button.name}
            boxShadow="0 2px 12px 0 rgba(0, 0, 0, 0.2)"
            fontSize="14px"
            width="164px"
            styleOverride={
              buttons.length === 1
                ? {
                    margin: 0,
                    marginLeft: '50%',
                    transform: 'translateX(-50%)',
                    float: 'none',
                  }
                : {}
            }
          />
        ))}
      </ButtonsContainer>
    ) : null}
    <Spacer />
  </WarningBox>
);
Warning.propTypes = {
  buttons: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      name: PropTypes.string,
      toolTip: PropTypes.string,
    })
  ),
  icon: PropTypes.string,
  iconSize: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
  iconColor: PropTypes.string,
  text: PropTypes.oneOfType([PropTypes.string, PropTypes.shape({})]),
  textAlign: PropTypes.string,
  textSize: PropTypes.number,
  title: PropTypes.string,
  open: PropTypes.bool,
  targetBounds: PropTypes.shape({}),
  hideWarningAction: PropTypes.func,
};
Warning.defaultProps = {
  buttons: [],
  icon: null,
  iconSize: { width: 64, height: 64 },
  iconColor: null,
  text: null,
  textAlign: 'left',
  textSize: 20,
  open: true,
  title: null,
  targetBounds: null,
  hideWarningAction: null,
};
export default Warning;
