import log from 'loglevel';
import _ from 'lodash';
import {
  FETCH_CAMERAS_FAILURE,
  FETCH_CAMERAS_SUCCESS,
  FETCH_CAMERAS_REQUEST,
  CamerasState,
  CamerasActionTypes,
} from './types';

const initialCamerasState: CamerasState = {
  isFetching: false,
  available: [{}],
};
export const camerasReducer = (
  state = initialCamerasState,
  action: CamerasActionTypes
): CamerasState => {
  switch (action.type) {
    case FETCH_CAMERAS_REQUEST:
      return {
        ...state,
        isFetching: true,
      };
    case FETCH_CAMERAS_SUCCESS:
      // TODO (Szucsi): Remove hack :)
      let hackedPayload = _.cloneDeep(action.payload);
      /*_.set(hackedPayload, '[0].size.width', 1920);*/
      return {
        ...state,
        isFetching: false,
        available: hackedPayload,
      };
    case FETCH_CAMERAS_FAILURE:
      log.error(`fetchCamerasRequestSaga error: ${action.payload}`);
      return state;
    default:
      return state;
  }
};
