import {
  SHOW_WARNING,
  HIDE_WARNING,
  ShowWarningAction,
  HideWarningAction,
  WarningState,
} from './types';

export const showWarningAction = (warningSettings: WarningState): ShowWarningAction => ({
  type: SHOW_WARNING,
  payload: warningSettings,
});
export const hideWarningAction = (): HideWarningAction => ({
  type: HIDE_WARNING,
});
