/* =============================================>>>>>
= CAMERAS =
===============================================>>>>> */

export const FETCH_CAMERAS_REQUEST = 'FETCH_CAMERAS_REQUEST';
export const FETCH_CAMERAS_FAILURE = 'FETCH_CAMERAS_FAILURE';
export const FETCH_CAMERAS_SUCCESS = 'FETCH_CAMERAS_SUCCESS';

export interface Camera {
  id?: string;
  size?: {
    height: number;
    width: number;
  };
}

export type CamerasMessage = Camera[];

export interface CamerasState {
  isFetching: boolean;
  available: CamerasMessage;
}

export interface FetchCamerasRequestAction {
  type: typeof FETCH_CAMERAS_REQUEST;
  payload: {
    callback: ((data?: any) => void) | null;
  };
}

export interface FetchCamerasSuccessAction {
  type: typeof FETCH_CAMERAS_SUCCESS;
  payload: CamerasMessage;
}

export interface FetchCamerasFailedAction {
  type: typeof FETCH_CAMERAS_FAILURE;
  payload: Error;
}

export type CamerasActionTypes =
  | FetchCamerasRequestAction
  | FetchCamerasSuccessAction
  | FetchCamerasFailedAction;
