import React, { useEffect } from 'react';
import Modal from '@material-ui/core/Modal';
import { useSelector } from 'react-redux';
import { PEND_UPDATE_REASON } from '../store/ui/enums';
const doNotPropagate = e => {
  e.stopPropagation();
};

const PreventClicks = React.forwardRef((props, ref) => {
  useEffect(() => {
    // useCapture is true because we want to stop event downstream and not upsteam
    // syntax - target.addEventListener(type, listener [, useCapture]);
    document.addEventListener('keyup', doNotPropagate, true);
    return () => {
      document.removeEventListener('keyup', doNotPropagate, true);
    };
  }, []);
  return <div ref={ref} />;
});

const PreventClicksAndKeys = () => {
  const { pendingUpdate } = useSelector(state => ({
    pendingUpdate: state.ui.pendingUpdate,
  }));
  return (
    <Modal
      open={
        !!pendingUpdate &&
        pendingUpdate !== PEND_UPDATE_REASON.LOADING_STEP_PROFILE_COMBO &&
        pendingUpdate !== PEND_UPDATE_REASON.FAILED_CAPTURE_FRAME
      }
      hideBackdrop
    >
      <div>
        <PreventClicks />
      </div>
    </Modal>
  );
};

export default PreventClicksAndKeys;
