import Emitter from 'component-emitter';
import parseuri from 'parseuri';
import Sockette from 'sockette';
import log from 'loglevel';

class ChannelSockette {
  constructor(uri, options) {
    Emitter(this);

    this.emitterAddEventListener = this.addEventListener;
    Emitter.prototype.addEventListener = this.notifiedAddEventListener;
    this.emitterRemoveEventListener = this.removeEventListener;
    Emitter.prototype.removeEventListener = this.notifiedRemoveEventListener;

    this.url = uri.url.replace(/(https?)/, 'ws');

    this.path = uri.path;

    this.options = {
      ...options,
      onmessage: ev => {
        const emitter = this;
        if (ev.data instanceof Blob) {
          emitter.emit('binary', ev.data);
        } else {
          const msg = ev.data.split('___ws___');
          const [channel, content] = msg;

          // if `content` is invalid, it means `channel` holds the content
          const actualContent = content || channel;
          try {
            const obj =
              typeof actualContent !== 'object'
                ? JSON.parse(actualContent)
                : actualContent;
            if (channel && content) {
              emitter.emit(channel, obj);
            } else {
              emitter.emit('message', obj);
            }
          } catch (err) {
            log.info(`ChannelSockette error: content=${actualContent}, err=${err}`);
          }
        }
      },
      onclose: () => {
        this.emit('disconnect');
      },
      onopen: () => {
        this.emit('connect');
        this.updateChannels();
      },
      onerror: ev => this.emit('connect_error', ev.data),
      onreconnect: () => {
        log.info('Reconnect');
      },
    };
  }

  send = data => !this.socket || this.socket.send(data);

  json = data => !this.socket || this.socket.json(data);

  open() {
    if (!this.socket) {
      this.socket = Sockette(this.url, this.options);
    } // else - nothing to do (existing instance == opened connection)
  }

  connect = () => this.open();

  close() {
    if (this.socket) {
      this.socket.close();
      delete this.socket;
    }
  }

  notifiedAddEventListener = (event, fn) => {
    this.emitterAddEventListener(event, fn);
    this.updateChannels();
  };

  notifiedRemoveEventListener = (event, fn) => {
    this.emitterRemoveEventListener(event, fn);
    this.updateChannels();
  };

  updateChannels() {
    const { _callbacks: callbacks, socket } = this;
    if (socket) {
      const channels = Object.keys(callbacks || {}).map(event => event.substring(1));
      socket.json({ channels });
    }
  }
}

const cache = {};
const defOpts = {
  forceNew: false,
  disableAutoConnect: false,
};

function lookup(url, opts) {
  const finalOpts = { ...defOpts, ...opts };

  const obj = parseuri(url);
  obj.url = url;

  const ipv6 = obj.host.indexOf(':') !== -1;
  const host = ipv6 ? `[${obj.host}]` : obj.host;
  const { path, query } = obj;
  const id = `${obj.protocol}://${host}:${obj.port}/${path}?${query}`;

  const cached = cache[id];
  const sameNamespace = cached && path === cached.path;
  const forceNew = finalOpts.forceNew || sameNamespace;
  const autoConnect = !finalOpts.disableAutoConnect;

  let io;
  if (forceNew) {
    log.info(`ignoring socket cache for ${id}`);
    io = new ChannelSockette(obj, finalOpts);
  } else {
    if (!cached) {
      cache[id] = new ChannelSockette(obj, finalOpts);
    }
    io = cache[id];
  }

  if (autoConnect) {
    io.connect();
  }

  return io;
}

export default lookup;
