import _ from 'lodash';
import log from 'loglevel';
import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  fetchFixiStateRequestAction,
  setError,
  updatePopupScreen,
  updateScreen,
  toggleSettingsPageAction,
  updateMiniProfileComboAction,
  pendUpdate,
  finishedUpdate,
  pendLoadingProfileCombo,
  showWarningAction,
  hideWarningAction,
  updateMiniSequenceAction,
  setFrameImageTimer,
  fetchCamerasRequestAction,
} from '../store/actions';
import api from '../store/api';
import { SYS_ERRORS } from '../store/errors/enums';
import { PROFILE_OLD_STAGES } from '../store/fixiState/enums';
import { SETTINGS_TYPES } from '../store/settings/enums';
import { SCREEN, POPUP_SCREEN, PEND_UPDATE_REASON } from '../store/ui/enums';
import { initialErrorState } from '../store/errors/reducers';
import { SysError } from '../store/errors/types';
import { ApiHost } from '../store/hosts';
import io from '../utils/cSockette';
import { toCamelCaseRecursive } from '../utils/Utils';
import ErrorDialog from './Common/ErrorDialog';
import LoadingModal from './Common/LoadingModal';
import waitForServer from './HOC/WaitForServer';
import TopBar from './TopBar';
import WarningDialog from './Common/WarningDialog';
import styled from 'styled-components';
import { makeStyles } from '@material-ui/styles';
import { DC_OPERATION } from './Settings/SettingsUtils';
import usePrevious from './Hooks/usePrevious';
import PreventClicksAndKeys from './PreventClicksAndKeys';
import { ColorPalette } from '../utils/StyleGuide';
import framesStore from '../store/framesStore';
import { frameFadeDuration } from './InspectionScreen/InspectionClock';
import { AppDispatch, RootStore } from '../store/reduxStore';
import { AnnotatedFrame } from '../store/types';
import {
  Sequence,
  ProfileCombo,
  FixiState,
  isProfileComboNonEmpty,
} from '../store/fixiState/types';
import ProfileCenterScreen from './ProfileCenter/ProfileCenterScreen';

const iconNewProfile = require('../images/New.svg');
const iconNewDisabledProfile = require('../images/New_Disabled.svg');
const iconInspection = require('../images/Inspection.svg');
const iconInspectionDisabled = require('../images/Inspection_DIS.svg');
const iconArchive = require('../images/Archive.svg');
const iconArchiveDisabled = require('../images/Archive_DIS.svg');
const iconDebugBundleWithVideoStart = require('../images/Debug_bundle_with_video_start.svg');
const iconDebugBundleWithVideoStop = require('../images/Debug_bundle_with_video_stop.svg');
const iconDebugBundleWithoutVideo = require('../images/Debug_bundle_without_video.svg');
const iconDebugStartVideoCapture = require('../images/Debug_start_video_capture.svg');
const iconDebugStopVideoCapture = require('../images/Debug_stop_video_capture.svg');
const iconWarning = require('../images/cross_in_circle.svg');

const useStyles = makeStyles({
  newProfile: {
    width: 38,
    height: 38,
  },
  inspection: {
    width: 54,
    height: 38,
  },
  archive: {
    width: 44,
    height: 38,
  },
  newProfileMargin: {
    marginRight: 56,
    marginTop: 24,
    marginBottom: 24,
  },
  inspectionMargin: {
    marginRight: 56,
    marginTop: 24,
    marginBottom: 24,
  },
  archiveMargin: {
    marginRight: 56,
    marginTop: 24,
    marginBottom: 24,
  },
});

const MainDiv = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  font-size: 15px;
  background: white;
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.23;
  letter-spacing: normal;
  text-align: left;
  color: black;
`;

/**
 * The entry point of our application
 * this component is in charge of
 * 1. TopBar - navigation and control
 * 2. routing according to screen
 * 3. popups according to popupScreen
 * 4. displaying specific errors, warnings and loading indication
 * 5. handling socket messages for
 * - load_profile_combination (isLoading)
 * - fixi_state (update our profileCombo, sequence, pendingUpdate and screen state)
 * - mini_profile_combo_update - update minimal info for profile manager
 * - mini_sequence_update - update minimal info for profile sequence manager
 * - sys_error - display errors
 * - plc_status - update warning according to plc status
 * - connect_error - crash and display errors
 * - disconnect - crash and display errors
 */
const App = () => {
  const socket = useRef<any>();
  const timeoutRef = useRef<number>(-1);
  const frameFadeTimeout = useRef<number>(-1);
  const [debugBundleWithVideoStarted, setDebugBundleWithVideoStarted] = useState(false);
  const [videoCaptureStarted, setVideoCaptureStarted] = useState(false);
  const dispatch: AppDispatch = useDispatch();
  useEffect(() => {
    dispatch(
      fetchCamerasRequestAction({
        callback: data => {
          log.info(`cameras: ${JSON.stringify(data)}`);
        },
      })
    );
    handleSocketMessages();
    api.reportBackendIsUp();
    return () => {
      if (socket.current) {
        socket.current.off();
        socket.current.close();
      }
      clearTimeout(timeoutRef.current);
      clearTimeout(frameFadeTimeout.current);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const {
    profileCombo,
    sequence,
    defaultVideoCaptureLength,
    savedVideoMaxLengthForSession,
    debugMode,
    screen,
    popupScreen,
    error,
    creatingNewProfileCombo,
    settingsPageType,
    settingsProfileCombinationId,
    isWarningVisible,
    warningSetup,
    cameraId,
    pendingUpdate,
    isOperationalMode,
    wasUnloaded,
  } = useSelector((state: RootStore) => {
    const { profileCombo, settings, ui, error, warning, cameras, sequence } = state;
    return {
      debugMode: settings.systemSettings.values['isDebugMode'],
      sequence,
      profileCombo,
      settingsPageType: settings.settingsPageType,
      settingsProfileCombinationId: settings.settingsProfileCombinationId,
      isKeepAdvancedSettings: settings.systemSettings.values['isKeepAdvancedSettings'],
      defaultVideoCaptureLength: settings.systemSettings.values[
        'userVideoMaxLength'
      ] as number,
      savedVideoMaxLengthForSession: settings.systemSettings.values[
        'debugBundleVideoMaxLength'
      ] as number,
      // TODO: Remove fixed screen when development is over on ProfileCenter.
      //  the original value was "ui.screen"
      screen: SCREEN.PROFILE_CENTER, // ui.screen,
      popupScreen: ui.popupScreen,
      error:
        error.id === SYS_ERRORS.SYSTEM_ERROR_OCCURRED ||
        ((error.id === SYS_ERRORS.PROCESS_STOPPED ||
          error.id === SYS_ERRORS.CAMERA_DISCONNECTED) &&
          ui.screen !== SCREEN.SETUP &&
          ui.screen !== SCREEN.INSPECTION)
          ? error
          : initialErrorState,
      creatingNewProfileCombo:
        ui.pendingUpdate === PEND_UPDATE_REASON.CREATING_NEW_PROFILE_COMBO,
      isWarningVisible: warning.isWarningVisible,
      warningSetup: warning.warningSetup,
      cameraId: _.get(cameras, 'available[0].id', '') as string,
      pendingUpdate: ui.pendingUpdate,
      isOperationalMode: settings.systemSettings.values['isOperationalMode'],
      wasUnloaded: ui.wasUnloaded,
    };
  });
  const classes = useStyles();

  // Effect to show/hide operational mode counter by listening to new frames
  // When we are in operational mode we get frames in sticky mode so when we
  // first connect to the socket in WithFrameStore we get a frame
  // When a new (not sticky) frame arrives, we need to update frame image timer
  const socketSubscriptionRef = useRef<(() => void) | null>(null);
  const isInspectionStage = _.get(profileCombo, 'profiles[0].stage', null) === 100;
  const isInspectionStagePrevious = usePrevious(isInspectionStage);
  const previousIsOperationalMode = usePrevious(isOperationalMode);
  useEffect(() => {
    const updateFrameImage = (frame: AnnotatedFrame | { is_sticky: boolean }) => {
      if (!frame.is_sticky) {
        dispatch(setFrameImageTimer(false));
      }
      clearTimeout(frameFadeTimeout.current);
      frameFadeTimeout.current = setTimeout(() => {
        dispatch(setFrameImageTimer(true));
      }, frameFadeDuration);
    };
    if (!socketSubscriptionRef.current && isOperationalMode && isInspectionStage) {
      socketSubscriptionRef.current = framesStore.subscribe(updateFrameImage);
    }
    if (isOperationalMode && isInspectionStage !== isInspectionStagePrevious) {
      updateFrameImage({ is_sticky: false });
    }
    // when we are in operational mode we set a sticky state so that we can show
    // it in FrameDrawer. In case we are not in operationalMode we do not want it
    // because when we open the ImageModal we load a frameDrawer and it sets that
    // stickyFrame on our canvas
    // fixes issue https://inspekto.atlassian.net/browse/INS-4002
    if (!isOperationalMode && previousIsOperationalMode) {
      framesStore.removeStickyState();
    }
    return () => {
      clearTimeout(frameFadeTimeout.current);
      dispatch(setFrameImageTimer(false));
      if (socketSubscriptionRef.current) {
        socketSubscriptionRef.current();
        socketSubscriptionRef.current = null;
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    previousIsOperationalMode,
    isOperationalMode,
    isInspectionStagePrevious,
    isInspectionStage,
  ]);

  const previousProfileComboId = usePrevious(
    _.get(profileCombo as ProfileCombo, 'id', '')
  );
  const previousSequenceActiveStep = usePrevious(
    _.get(sequence as Sequence, 'activeStep', -1)
  );

  useEffect(() => {
    if (
      previousProfileComboId !== _.get(profileCombo, 'id', '') ||
      previousSequenceActiveStep !== _.get(sequence, 'activeStep', -1)
    )
      setVideoCaptureStarted(false);
  }, [previousProfileComboId, profileCombo, previousSequenceActiveStep, sequence]);

  const dispatchServerError = () => {
    dispatch(
      setError({
        id: SYS_ERRORS.SYSTEM_ERROR_OCCURRED,
        value:
          'Sorry, an unexpected error occurred. Please wait, the system is repairing itself',
      })
    );
  };

  const errorProcess = async (callback: () => void) => {
    try {
      const res = await api.getDataCoordinatorStatus();
      if (
        [DC_OPERATION.UPGRADE.toLowerCase(), DC_OPERATION.RESTORE.toLowerCase()].includes(
          res.operation.toLowerCase()
        )
      ) {
        // Do Nothing
      } else {
        callback();
      }
    } catch {
      callback();
    }
  };

  const handleSocketMessages = () => {
    socket.current = io(`${ApiHost}`);
    socket.current.on('connect', () => {
      log.info('App handleSocketMessages socket connected');
    });
    socket.current.on('load_profile_combination', (data: any) => {
      try {
        const camelCaseData = toCamelCaseRecursive(data);
        if (camelCaseData.isLoading) {
          dispatch(pendLoadingProfileCombo(toCamelCaseRecursive(camelCaseData)));
        } else {
          dispatch(finishedUpdate());
        }
      } catch (e) {
        dispatch(finishedUpdate());
      }
    });
    socket.current.on('fixi_state', (snakeCaseFixiState: any) => {
      const fixiState: FixiState = toCamelCaseRecursive(snakeCaseFixiState);
      dispatch(
        fetchFixiStateRequestAction({
          fixiState,
        })
      );
    });
    socket.current.on('mini_profile_combo_update', (miniProfileCombo: any) => {
      // socket objects get added the is_sticky key but we don't want in this case
      // because saga assumes each key to be a profileComboId and value to be
      // miniProfileCombo info
      delete miniProfileCombo.is_sticky;
      dispatch(updateMiniProfileComboAction(toCamelCaseRecursive(miniProfileCombo)));
    });
    socket.current.on('mini_sequence_update', (miniSequence: any) => {
      // socket objects get added the is_sticky key but we don't want in this case
      // because saga assumes each key to be a sequenceId and value to be
      // miniSequence info
      delete miniSequence.is_sticky;
      dispatch(updateMiniSequenceAction(toCamelCaseRecursive(miniSequence)));
    });
    socket.current.on('sys_error', async (error: any) => {
      const sysError: SysError = toCamelCaseRecursive(error);
      await errorProcess(() => dispatch(setError(sysError)));
    });
    socket.current.on('plc_status', async (status: any) => {
      if (status.connection_status === 'false') {
        dispatch(
          showWarningAction({
            icon: iconWarning,
            iconColor: ColorPalette.solidRed,
            title: 'PLC Disconnected',
            text: 'The PLC Has Suddenly Disconnected',
            buttons: [
              {
                name: 'OK',
                text: 'OK',
                color: ColorPalette.darkSkyBlue,
                buttonCallback: () => {},
              },
            ],
            hasClose: true,
          })
        );
      } else {
        dispatch(hideWarningAction());
      }
    });
    socket.current.on('connect_error', async () => {
      await errorProcess(dispatchServerError);
    });
    socket.current.on('disconnect', async () => {
      await errorProcess(dispatchServerError);
    });
  };

  const generateOnTopBarButtonClick = (appContentKey: any) => () => {
    if (appContentKey === POPUP_SCREEN.SETTINGS) {
      dispatch(
        toggleSettingsPageAction({
          settingsPageType: SETTINGS_TYPES.SYSTEM,
          settingsProfileCombinationId: null,
        })
      );
    }
    // appContentKey is a popUp
    if (Object.values(POPUP_SCREEN).indexOf(appContentKey) > -1) {
      dispatch(updatePopupScreen(appContentKey));
    } else {
      dispatch(updateScreen(appContentKey));
    }
  };

  const onCameraIconClick = () => {
    if (inspectionMode) {
      dispatch(updateScreen(SCREEN.INSPECTION));
    } else if (screen === SCREEN.SETUP && finishedSetup && readyAndValidated) {
      dispatch(pendUpdate(PEND_UPDATE_REASON.MOVING_TO_INSPECTION));
      api.setStage({
        profileId: activeProfileId,
        stage: PROFILE_OLD_STAGES.INSPECTION_MODE,
      });
    } else {
      dispatch(updateScreen(SCREEN.SETUP));
    }
  };

  const profileName: string = _.get(profileCombo, 'profiles[0].name', '');
  const activeProfileId: string = _.get(profileCombo, 'profiles[0].id', '');
  const readyAndValidated =
    _.get(profileCombo, 'profiles[0].areReferencesValidated', false) &&
    _.get(profileCombo, 'profiles[0].referencesStatus.progressStatus', -1) === 2;
  const finishedSetup = _.get(
    profileCombo,
    'profiles[0].stageMarkers.isFinishedSetup',
    false
  );
  const passedAgc =
    _.get(profileCombo, 'profiles[0].stage', -1) >
    PROFILE_OLD_STAGES.IMPROVE_MARKING_AFTER_AGC;
  const inspectionMode =
    _.get(profileCombo, 'profiles[0].stage', -1) === PROFILE_OLD_STAGES.INSPECTION_MODE;
  let mainScreen = null;

  /* switch (screen) {
    case SCREEN.MAIN:
      mainScreen = (
        <MainScreen
          onClick={async (screen: SCREEN | POPUP_SCREEN) => {
            if (screen === POPUP_SCREEN.NEW_PROFILE_OR_SEQUENCE) {
              dispatch(updatePopupScreen(POPUP_SCREEN.NEW_PROFILE_OR_SEQUENCE));
            } else {
              dispatch(updateScreen(screen as SCREEN));
            }
          }}
          disable={!finishedSetup}
          wasUnloaded={wasUnloaded}
        />
      );
      break;
    case SCREEN.NEW_PROFILE_COMBO:
      mainScreen = <NewProfileComboScreen />;
      break;
    case SCREEN.SETUP:
      mainScreen = <SetupScreen key={`${activeProfileId}`} />;
      break;
    case SCREEN.ARCHIVE:
      if (isProfileComboNonEmpty(profileCombo)) {
        mainScreen = (
          <ArchiveScreen key="archive-screen" profile={profileCombo.profiles[0]} />
        );
      } else {
        log.error('trying to load archive for an empty profileCombo');
      }
      break;
    case SCREEN.INSPECTION:
      mainScreen = <InspectionManagerScreen />;
      break;
    case SCREEN.SETUP_PLANT:
      mainScreen = <SetupPlantScreen />;
      break;
    case SCREEN.PROFILE_MANAGEMENT:
      mainScreen = (
        <ProfileManagerScreen
          activeProfileId={activeProfileId}
          activeProfileComboId={_.get(profileCombo, 'id', '')}
          handleNewProfile={async () => {
            await api.createProfileCombo({ cameraId });
          }}
          handleNewSequence={async () => {
            await api.createSequence();
          }}
        />
      );
      break;
    case SCREEN.SEQUENCE_SETUP:
      mainScreen = <SequenceSetupScreen />;
      break;
    case SCREEN.PROFILE_CENTER:
      mainScreen = (
        null
      );
      break;
    default:
      log.error(`Unknown key: ${screen}`);
  } */
  let mainPopupScreen = null;
  /* switch (popupScreen) {
    case POPUP_SCREEN.ABOUT:
      mainPopupScreen = (
        <AboutPage onClose={() => dispatch(updatePopupScreen(POPUP_SCREEN.NO_POPUP))} />
      );
      break;
    case POPUP_SCREEN.SETTINGS:
      mainPopupScreen = (
        <SettingsPage
          pageType={settingsPageType}
          settingsProfileCombinationId={settingsProfileCombinationId}
          settingsCloseCallback={() => dispatch(updatePopupScreen(POPUP_SCREEN.NO_POPUP))}
          title={
            settingsPageType === SETTINGS_TYPES.SYSTEM
              ? 'S70 Settings'
              : 'Profile settings'
          }
        />
      );
      break;
    case POPUP_SCREEN.NEW_PROFILE_OR_SEQUENCE:
      mainPopupScreen = (
        <NewProfileOrSequencePopup
          handleProfileClick={async () => {
            dispatch(pendUpdate(PEND_UPDATE_REASON.LOADING_PROFILE_COMBO));
            dispatch(updatePopupScreen(POPUP_SCREEN.NO_POPUP));
            await api.createProfileCombo({ cameraId });
          }}
          handleSequenceClick={async () => {
            dispatch(pendUpdate(PEND_UPDATE_REASON.LOADING_PROFILE_COMBO));
            dispatch(updatePopupScreen(POPUP_SCREEN.NO_POPUP));
            await api.createSequence();
          }}
          onClose={() => dispatch(updatePopupScreen(POPUP_SCREEN.NO_POPUP))}
        />
      );
      break;
    case POPUP_SCREEN.IMAGE_MODAL:
    case POPUP_SCREEN.NO_POPUP:
      break;
    default:
      log.error(`Unknown key: ${mainPopupScreen}`);
  } */

  const topBarButtons = [];

  if (debugMode && isProfileComboNonEmpty(profileCombo) && profileCombo.id && passedAgc) {
    topBarButtons.push(
      {
        alt: 'DebugBundleWithVideo',
        icon: debugBundleWithVideoStarted
          ? iconDebugBundleWithVideoStart
          : iconDebugBundleWithVideoStop,
        onClick: () => {
          if (!debugBundleWithVideoStarted && !videoCaptureStarted) {
            setDebugBundleWithVideoStarted(true);
            api.createDebugBundle(true);
            setTimeout(() => {
              setDebugBundleWithVideoStarted(false);
            }, savedVideoMaxLengthForSession * 1000);
          }
        },
        customizedButtonImageClass: classes.archive,
        customizedButtonMarginClass: classes.archiveMargin,
      },
      {
        alt: 'DebugBundleWithoutVideo',
        icon: iconDebugBundleWithoutVideo,
        onClick: () => api.createDebugBundle(false),
        customizedButtonImageClass: classes.archive,
        customizedButtonMarginClass: classes.archiveMargin,
      },
      {
        alt: 'DebugVideoCapture',
        icon: videoCaptureStarted
          ? iconDebugStopVideoCapture
          : iconDebugStartVideoCapture,
        onClick: () => {
          clearTimeout(timeoutRef.current);
          if (videoCaptureStarted) {
            setVideoCaptureStarted(false);
            api.stopVideoCapture();
          } else if (!debugBundleWithVideoStarted) {
            setVideoCaptureStarted(true);
            api.startVideoCapture(defaultVideoCaptureLength);
            timeoutRef.current = setTimeout(() => {
              setVideoCaptureStarted(false);
            }, defaultVideoCaptureLength * 1000);
          }
        },
        customizedButtonImageClass: classes.archive,
        customizedButtonMarginClass: classes.archiveMargin,
      }
    );
  }

  topBarButtons.push(
    {
      alt: 'New Profile',
      icon:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        screen === SCREEN.NEW_PROFILE_COMBO ||
        screen === SCREEN.SEQUENCE_SETUP ||
        creatingNewProfileCombo
          ? iconNewDisabledProfile
          : iconNewProfile,
      onClick: () => dispatch(updatePopupScreen(POPUP_SCREEN.NEW_PROFILE_OR_SEQUENCE)),
      customizedButtonImageClass: classes.archive,
      customizedButtonMarginClass: classes.archiveMargin,
      disabled:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        screen === SCREEN.NEW_PROFILE_COMBO ||
        screen === SCREEN.SEQUENCE_SETUP ||
        creatingNewProfileCombo,
    },
    {
      alt: 'Inspection Mode',
      icon:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        screen === SCREEN.NEW_PROFILE_COMBO ||
        screen === SCREEN.SEQUENCE_SETUP ||
        (screen === SCREEN.SETUP && !finishedSetup) ||
        (screen === SCREEN.SETUP && finishedSetup && !readyAndValidated) ||
        !profileName
          ? iconInspectionDisabled
          : iconInspection,
      onClick: onCameraIconClick,
      customizedButtonImageClass: classes.inspection,
      customizedButtonMarginClass: classes.inspectionMargin,
      disabled:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        screen === SCREEN.NEW_PROFILE_COMBO ||
        screen === SCREEN.SEQUENCE_SETUP ||
        (screen === SCREEN.SETUP && !finishedSetup) ||
        (screen === SCREEN.SETUP && finishedSetup && !readyAndValidated) ||
        !profileName,
    },
    {
      alt: 'Archive',
      icon:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        screen === SCREEN.NEW_PROFILE_COMBO ||
        screen === SCREEN.SEQUENCE_SETUP ||
        !finishedSetup ||
        creatingNewProfileCombo
          ? iconArchiveDisabled
          : iconArchive,
      onClick: generateOnTopBarButtonClick(SCREEN.ARCHIVE),
      customizedButtonImageClass: classes.archive,
      customizedButtonMarginClass: classes.archiveMargin,
      disabled:
        popupScreen === POPUP_SCREEN.SETTINGS ||
        !finishedSetup ||
        creatingNewProfileCombo,
    }
  );

  return (
    <MainDiv>
      {screen !== SCREEN.SETUP && screen !== SCREEN.PROFILE_CENTER && (
        <TopBar
          position="sticky"
          buttons={screen === SCREEN.MAIN ? [] : topBarButtons}
          generateOnTopBarButtonClick={generateOnTopBarButtonClick}
        />
      )}
      {mainScreen}
      {mainPopupScreen}
      <LoadingModal
        open={
          [
            PEND_UPDATE_REASON.LOADING_PROFILE_COMBO,
            PEND_UPDATE_REASON.SET_INSPECTION_AS_REFERENCE,
            PEND_UPDATE_REASON.MOVING_TO_INSPECTION,
            PEND_UPDATE_REASON.STARTING_SEQUENCE,
            PEND_UPDATE_REASON.LOADING_SEQUENCE,
          ].includes(pendingUpdate) &&
          !!cameraId &&
          screen !== SCREEN.SETUP
        }
        message={
          pendingUpdate === PEND_UPDATE_REASON.LOADING_PROFILE_COMBO
            ? 'Loading profile'
            : null
        }
      />
      <ErrorDialog error={error} />
      <WarningDialog
        open={isWarningVisible}
        buttons={warningSetup.buttons}
        icon={warningSetup.icon}
        iconColor={warningSetup.iconColor}
        text={warningSetup.text}
        title={warningSetup.title}
        targetBounds={warningSetup.targetBounds}
        hasClose={warningSetup.hasClose}
      />
      <PreventClicksAndKeys />
    </MainDiv>
  );
};

export default waitForServer(App);
