import {
  PendLoadingProfileMessage,
  UpdateScreen,
  UpdatePopupScreen,
  PendUpdate,
  FinishedUpdate,
  StateWasUnloaded,
  PendLoadingProfileCombo,
  UpdateFrameImageTimer,
  SetFrameImageTimer,
  UPDATE_SCREEN,
  UPDATE_POPUP_SCREEN,
  PEND_UPDATE,
  FINISHED_UPDATE,
  PEND_LOADING_PROFILE_COMBO,
  UPDATE_FRAME_IMAGE_TIMER,
  SET_FRAME_IMAGE_TIMER,
  STATE_WAS_UNLOADED,
} from './types';
import { SCREEN, POPUP_SCREEN, PEND_UPDATE_REASON } from './enums';

export const updateScreen = (screen: SCREEN): UpdateScreen => ({
  type: UPDATE_SCREEN,
  payload: screen,
});

export const updatePopupScreen = (popupScreen: POPUP_SCREEN): UpdatePopupScreen => ({
  type: UPDATE_POPUP_SCREEN,
  payload: popupScreen,
});

export const pendUpdate = (reason: PEND_UPDATE_REASON): PendUpdate => ({
  type: PEND_UPDATE,
  payload: reason,
});

export const finishedUpdate = (): FinishedUpdate => ({
  type: FINISHED_UPDATE,
});

export const stateWasUnloaded = (wasUnloaded: boolean = false): StateWasUnloaded => ({
  type: STATE_WAS_UNLOADED,
  payload: wasUnloaded,
});

export const pendLoadingProfileCombo = ({
  isLoading,
  profileComboId,
  sequenceId,
}: PendLoadingProfileMessage): PendLoadingProfileCombo => ({
  type: PEND_LOADING_PROFILE_COMBO,
  payload: {
    isLoading,
    profileComboId,
    sequenceId,
  },
});

export const updateFrameImageTimer = (time: number): UpdateFrameImageTimer => ({
  type: UPDATE_FRAME_IMAGE_TIMER,
  payload: time,
});

export const setFrameImageTimer = (bool: boolean): SetFrameImageTimer => ({
  type: SET_FRAME_IMAGE_TIMER,
  payload: bool,
});
