export const FETCH_SYSTEM_TIME_SETTINGS_REQUEST = 'FETCH_SYSTEM_TIME_SETTINGS_REQUEST';
export const FETCH_SYSTEM_TIME_SETTINGS_SUCCESS = 'FETCH_SYSTEM_TIME_SETTINGS_SUCCESS';
export const SET_SYSTEM_TIME_SETTINGS_REQUEST = 'SET_SYSTEM_TIME_SETTINGS_REQUEST';

export interface TimeZoneOption {
  displayName: string;
  key: string;
  name: string;
}
export interface SystemTime {
  metadata: {
    fields: {
      timeZone: {
        options: [TimeZoneOption];
      };
    };
  };
  values: {
    timeZone: string;
    useNtp: boolean;
  };
}

export interface SetSystemTimeMessage {
  key: string;
  value: string;
}

export interface SystemTimeState {
  useNtp: boolean;
  isFetching: boolean;
  timeZone: string;
  timeZoneOptions: TimeZoneOption[];
}

export interface FetchSystemTimeRequestAction {
  type: typeof FETCH_SYSTEM_TIME_SETTINGS_REQUEST;
  payload: { callback?: (() => void) | null };
}

export interface FetchSystemTimeSuccessAction {
  type: typeof FETCH_SYSTEM_TIME_SETTINGS_SUCCESS;
  payload: SystemTime;
}

export interface SetSystemTimeRequestAction {
  type: typeof SET_SYSTEM_TIME_SETTINGS_REQUEST;
  payload: SetSystemTimeMessage;
}

export type SystemTimeActionTypes =
  | FetchSystemTimeRequestAction
  | FetchSystemTimeSuccessAction
  | SetSystemTimeRequestAction;
