import {
  FETCH_SYSTEM_SETTINGS_REQUEST,
  FETCH_SYSTEM_SETTINGS_SUCCESS,
  FETCH_PROFILE_SETTINGS_REQUEST,
  FETCH_PROFILE_SETTINGS_SUCCESS,
  TOGGLE_SETTINGS_PAGE,
  Settings,
  ToggleSettingsPageMessage,
  FetchProfileSettingsMessage,
  FetchSystemSettingsMessage,
  FetchSystemSettingsRequestAction,
  FetchSystemSettingsSuccessAction,
  FetchProfileSettingsRequestAction,
  FetchProfileSettingsSuccessAction,
  ToggleSettingsPageAction,
} from './types';

export const fetchSystemSettingsRequestAction = ({
  callback = null,
  password = '',
  isGetMetadata = true,
}: FetchSystemSettingsMessage): FetchSystemSettingsRequestAction => ({
  type: FETCH_SYSTEM_SETTINGS_REQUEST,
  payload: { callback, password, isGetMetadata },
});

export const fetchSystemSettingsSuccessAction = (
  data: Settings
): FetchSystemSettingsSuccessAction => ({
  type: FETCH_SYSTEM_SETTINGS_SUCCESS,
  payload: data,
});

export const fetchProfileSettingsRequestAction = ({
  callback = null,
  password = '',
  isGetMetadata = true,
  profileId,
}: FetchProfileSettingsMessage): FetchProfileSettingsRequestAction => ({
  type: FETCH_PROFILE_SETTINGS_REQUEST,
  payload: { callback, password, isGetMetadata, profileId },
});

export const fetchProfileSettingsSuccessAction = (
  data: Settings
): FetchProfileSettingsSuccessAction => ({
  type: FETCH_PROFILE_SETTINGS_SUCCESS,
  payload: data,
});

export const toggleSettingsPageAction = ({
  settingsPageType,
  settingsProfileCombinationId,
}: ToggleSettingsPageMessage): ToggleSettingsPageAction => ({
  type: TOGGLE_SETTINGS_PAGE,
  payload: { settingsPageType, settingsProfileCombinationId },
});
