import {
  FETCH_SYSTEM_TIME_SETTINGS_REQUEST,
  FETCH_SYSTEM_TIME_SETTINGS_SUCCESS,
  SET_SYSTEM_TIME_SETTINGS_REQUEST,
  SystemTime,
  SetSystemTimeMessage,
  FetchSystemTimeRequestAction,
  FetchSystemTimeSuccessAction,
  SetSystemTimeRequestAction,
} from './types';

export const fetchSystemTimeRequestAction = ({
  callback = null,
}): FetchSystemTimeRequestAction => ({
  type: FETCH_SYSTEM_TIME_SETTINGS_REQUEST,
  payload: { callback },
});

export const fetchSystemTimeSuccessAction = (
  data: SystemTime
): FetchSystemTimeSuccessAction => ({
  type: FETCH_SYSTEM_TIME_SETTINGS_SUCCESS,
  payload: data,
});

export const setSystemTimeRequestAction = ({
  key,
  value,
}: SetSystemTimeMessage): SetSystemTimeRequestAction => ({
  type: SET_SYSTEM_TIME_SETTINGS_REQUEST,
  payload: { key, value },
});
