export const getMenuPosition = (
  show: boolean,
  direction: ANIMATION_DIRECTION,
  menuArea: Area,
  adjustment: { inPos: Point; outPos: Point } = {
    inPos: { x: 0, y: 0 },
    outPos: { x: 0, y: 0 },
  }
): Point => {
  // add more directions if needed
  let position = { x: 0, y: 0 };
  switch (direction) {
    case ANIMATION_DIRECTION.TOP:
      position = {
        x: 0,
        y: show ? 0 : -menuArea.height,
      };
      break;
    case ANIMATION_DIRECTION.LEFT:
      position = {
        x: show ? 0 : -menuArea.width,
        y: 0,
      };
      break;
    default:
      break;
  }
  const adjustPosition = show ? adjustment.inPos : adjustment.outPos;
  return { x: position.x + adjustPosition.x, y: position.y + adjustPosition.y };
};

export const getBreadcrumbText = (activeId: DASH_MENU, menuSetup: MenuSetup[]) =>
  menuSetup.reduce(
    (acc: string | null, curr: MenuSetup) => (curr.id === activeId ? curr.text : acc),
    null
  );
