import log from 'loglevel';
import { eventChannel, END } from 'redux-saga';
import { all, call, put, select, takeEvery, take } from 'redux-saga/effects';
import * as actions from './actions';
import api from './api';
import { PEND_UPDATE_REASON, SCREEN } from './ui/enums';
import { PROFILE_STAGES, FRAME_STATUS } from './fixiState/enums';
import { imagePrefix } from './framesSocket';
import { FETCH_CAMERAS_REQUEST, FetchCamerasRequestAction } from './cameras/types';
import { FETCH_FIXI_STATE_REQUEST, FetchFixiStateRequestAction } from './fixiState/types';
import { emptyMarkingData } from './consts';
import {
  FETCH_MINI_PROFILE_COMBOS_REQUEST,
  FETCH_MINI_SEQUENCES_REQUEST,
  UPDATE_MINI_PROFILE_COMBO,
  MiniProfileCombosState,
  MiniSequencesState,
  FetchMiniProfileCombosRequestAction,
  UpdateMiniProfileComboAction,
  FetchMiniSequencesRequestAction,
} from './miniState/types';
import {
  FETCH_SYSTEM_SETTINGS_REQUEST,
  FETCH_PROFILE_SETTINGS_REQUEST,
  FetchSystemSettingsRequestAction,
  FetchProfileSettingsRequestAction,
} from './settings/types';
import { SET_FAILED_FRAME_AS_FROZEN_FRAME, FrozenFrame } from './frozenFrame/types';
import {
  FETCH_SYSTEM_TIME_SETTINGS_REQUEST,
  SET_SYSTEM_TIME_SETTINGS_REQUEST,
  FetchSystemTimeRequestAction,
  SetSystemTimeRequestAction,
} from './systemTime/types';
import {
  PEND_LOADING_PROFILE_COMBO,
  SET_FRAME_IMAGE_TIMER,
  PendLoadingProfileCombo,
  SetFrameImageTimer,
} from './ui/types';
import _ from 'lodash';

export function* fetchCamerasRequestSaga(action: FetchCamerasRequestAction) {
  try {
    const { callback } = action.payload;
    const response = yield call(api.cameras);
    yield put(actions.fetchCamerasSuccessAction(response));
    if (callback) {
      callback();
    }
  } catch (err) {
    log.error(`fetchCamerasRequestSaga error: ${err}`);
    yield put(actions.fetchCamerasFailedAction(err));
  }
}

export function* fetchFixiStateRequestSaga(action: FetchFixiStateRequestAction) {
  try {
    const { callback, fixiState } = action.payload;

    let response;
    if (fixiState) {
      response = fixiState;
    } else {
      response = yield call(api.getFixiState);
    }

    const activeSequence = response.activeSequence;
    const activeProfileCombo = response.activeProfileCombo;
    const wasUnloaded = response.wasUnloaded;

    const {
      ui,
      profileCombo: previousProfileCombo,
      sequence: previousSequence,
    } = yield select();

    yield put(actions.setActiveSequenceAction(activeSequence));

    if (!activeSequence && !activeProfileCombo) {
      if (ui.screen !== SCREEN.PROFILE_MANAGEMENT) {
        yield put(actions.updateScreen(SCREEN.MAIN));
        yield put(actions.stateWasUnloaded(wasUnloaded));
      }
      yield put(actions.setActiveProfileComboAction(activeProfileCombo || {}));
    } else if (activeSequence && !activeProfileCombo) {
      // this is how we know we are editing a sequence
      yield put(actions.updateScreen(SCREEN.SEQUENCE_SETUP));
      yield put(actions.setActiveProfileComboAction(activeProfileCombo || {}));
      yield put(actions.finishedUpdate());
    } else if (!activeSequence && activeProfileCombo && !activeProfileCombo.name) {
      // this is how we know we are creating a new profile combo
      // When we updateScreen to SETUP
      // we need to make sure we have a profileCombo with a profile inside
      // so if we have a profile already we can move to SetupScreen
      // else we need to populate the profile combo before moving to SetupScreen
      if (_.get(previousProfileCombo, 'profiles[0]')) {
        yield put(actions.updateScreen(SCREEN.SETUP));
        yield put(actions.setActiveProfileComboAction(activeProfileCombo));
      } else {
        yield put(actions.setActiveProfileComboAction(activeProfileCombo));
        yield put(actions.updateScreen(SCREEN.SETUP));
      }
    } else {
      activeProfileCombo.lastAutoGeneratedImageName = response.lastAutoGeneratedImageName;

      const newProfile = previousProfileCombo.id !== activeProfileCombo.id;

      const newSequence =
        _.get(previousSequence, 'id', -1) !== _.get(activeSequence, 'id', -1) ||
        _.get(previousSequence, 'draft.isActive', -1) !==
          _.get(activeSequence, 'draft.isActive', -1);

      const profile = activeProfileCombo.profiles[0];
      const { stageMarkers, stage } = profile;

      // handle frozenFrame if exists
      const isFrozenFrame =
        stageMarkers.editedReference ||
        stageMarkers.referenceCandidateFromInspectionImageStr ||
        stageMarkers.referenceCandidateFromVideoImageStr;
      if (!isFrozenFrame) {
        yield put(actions.removeFrozenFrame());
      } else {
        if (stage === PROFILE_STAGES.DURING_AGC) {
          // ignore frozen frame while agc is happening
          yield put(actions.removeFrozenFrame());
          yield put(actions.pendUpdate(PEND_UPDATE_REASON.AGC));
        } else if (
          stageMarkers.referenceCandidateFromVideo &&
          stageMarkers.referenceCandidateFromVideo.frameStatus ===
            FRAME_STATUS.ADD_FRAME_REQUEST_REJECTED
        ) {
          // ignore frozen frame when frame was rejected
          yield put(actions.removeFrozenFrame());
          yield put(actions.pendUpdate(PEND_UPDATE_REASON.FAILED_CAPTURE_FRAME));
        } else if (stageMarkers.referenceCandidateFromVideoImageStr) {
          // handle frozenFrame from setup
          let polygons = emptyMarkingData;
          if (stageMarkers.editedMarkingData) {
            polygons = stageMarkers.editedMarkingData;
          }
          const frozenFrame = {
            frame: `${imagePrefix}${stageMarkers.referenceCandidateFromVideoImageStr}`,
            polygons,
            status: stageMarkers.referenceCandidateFromVideo.frameStatus,
          };
          yield put(actions.createFrozenFrame(frozenFrame));
        } else if (
          stageMarkers.referenceCandidateFromInspectionId &&
          stageMarkers.referenceCandidateFromInspectionImageStr
        ) {
          // handle frozenFrame from inspection
          let polygons = emptyMarkingData;
          const {
            editedMarkingData,
            referenceCandidateFromInspectionImageStr,
          } = stageMarkers;
          if (editedMarkingData) {
            editedMarkingData.defects = [];
            polygons = editedMarkingData;
          }
          const frozenFrame: FrozenFrame = {
            frame: `${imagePrefix}${referenceCandidateFromInspectionImageStr}`,
            polygons,
            status: FRAME_STATUS.OK,
          };
          yield put(actions.createFrozenFrame(frozenFrame));
        }
      }

      yield put(actions.setActiveProfileComboAction(activeProfileCombo || {}));

      // handle stage
      if (
        ui.screen !== SCREEN.SETUP &&
        ui.screen !== SCREEN.ARCHIVE &&
        (ui.screen !== SCREEN.PROFILE_MANAGEMENT || newProfile || newSequence) &&
        stage !== PROFILE_STAGES.INSPECTION_MODE
      ) {
        // need to go to setup screen
        yield put(actions.updateScreen(SCREEN.SETUP));
      } else if (
        ui.screen !== SCREEN.INSPECTION &&
        ui.screen !== SCREEN.ARCHIVE &&
        (ui.screen !== SCREEN.PROFILE_MANAGEMENT ||
          (!activeSequence && newProfile) ||
          newSequence) &&
        stage === PROFILE_STAGES.INSPECTION_MODE
      ) {
        const isReadyForInspection = profile.referencesStatus.progressStatus === 2;
        if (isReadyForInspection && profile.areReferencesValidated) {
          // we can to go to inspection screen
          yield put(actions.updateScreen(SCREEN.INSPECTION));
        } else {
          // crash the backend if we get here
          global.critical = true;
          log.error(
            'backend state problem\n' +
              'stage is inspection mode but screen cannot go to inspection if we are not ready and validated\n' +
              `stage ${stage}\n` +
              `isReadyForInspection ${isReadyForInspection}\n` +
              `areReferencesValidated ${profile.areReferencesValidated}`
          );
        }
      }

      // Taking UI state again in case it changed during this saga
      // specifically pendingUpdate
      const { ui: latestUI } = yield select();

      // finish update
      if (
        latestUI.pendingUpdate &&
        // we handle failed capture frame finish update
        // in another saga setFailedFrameAsFrozenFrame
        latestUI.pendingUpdate !== PEND_UPDATE_REASON.FAILED_CAPTURE_FRAME &&
        stage !== PROFILE_STAGES.DURING_AGC &&
        // we handle loading profile combo finished in app.js socket msg
        latestUI.pendingUpdate !== PEND_UPDATE_REASON.LOADING_PROFILE_COMBO
      ) {
        yield put(actions.finishedUpdate());
      }
    }
    if (callback) {
      callback();
    }
  } catch (err) {
    log.error(`fetchFixiStateRequestSaga error: ${err}`);
    yield put(actions.fetchFixiStateFailedAction(err));
  }
}

export function* setFailedFrameAsFrozenFrame() {
  try {
    const { profileCombo } = yield select();
    const { stageMarkers, id: profileId } = profileCombo.profiles[0];
    let polygons = [];
    if (stageMarkers.editedMarkingData) {
      polygons = stageMarkers.editedMarkingData;
    }
    const frozenFrame = {
      frame: `${imagePrefix}${stageMarkers.referenceCandidateFromVideoImageStr}`,
      polygons,
      status: stageMarkers.referenceCandidateFromVideo.frameStatus,
    };
    yield put(actions.createFrozenFrame(frozenFrame));
    yield put(actions.finishedUpdate());
    yield call(api.setRejectedFrameAsOk, profileId);
  } catch (err) {
    log.error(`setFailedFrameAsFrozenFrame error: ${err}`);
  }
}

export function* fetchSystemSettingsRequestSaga(
  action: FetchSystemSettingsRequestAction
) {
  const { callback, password, isGetMetadata } = action.payload;
  try {
    const response = yield call(api.settings, password, isGetMetadata);
    yield put(actions.fetchSystemSettingsSuccessAction(response));
    if (callback) {
      yield callback(response.isAuthenticated);
    }
  } catch (err) {
    log.error(`fetchSystemSettingsRequestSaga error: ${err}`);
  }
}

export function* fetchProfileSettingsRequestSaga(
  action: FetchProfileSettingsRequestAction
) {
  const { callback, password, isGetMetadata, profileId } = action.payload;
  try {
    const response = yield call(api.profileSettings, password, isGetMetadata, profileId);

    yield put(actions.fetchProfileSettingsSuccessAction(response));
    if (callback) {
      yield callback(response.isAuthenticated);
    }
  } catch (err) {
    log.error(`fetchProfileSettingsRequestSaga error: ${err}`);
  }
}

const addSequenceNames = (
  miniProfileCombos: MiniProfileCombosState,
  miniSequences: MiniSequencesState
) => {
  Object.keys(miniProfileCombos).forEach(miniProfileComboId => {
    miniProfileCombos[miniProfileComboId].relatedSequences.forEach(relatedSequence => {
      if (miniSequences[relatedSequence.id]) {
        relatedSequence.name = miniSequences[relatedSequence.id].name;
      } else {
        log.error(
          `miniProfileCombo ${miniProfileComboId} ` +
            `related to sequence ${relatedSequence.id} does not exist`
        );
      }
    });
  });
};

export function* fetchMiniProfileCombosRequestSaga(
  action: FetchMiniProfileCombosRequestAction
) {
  try {
    const { callback } = action.payload;
    const response = yield call(api.getAllProfileCombo);
    const { miniSequences } = yield select();
    addSequenceNames(response, miniSequences);
    yield put(actions.fetchMiniProfileCombosSuccessAction(response));
    if (callback) {
      yield callback();
    }
  } catch (err) {
    log.error(`fetchMiniProfileCombosRequestSaga error: ${err}`);
    yield put(actions.fetchMiniProfileCombosFailedAction(err));
  }
}

export function* updateMiniProfileComboSaga(action: UpdateMiniProfileComboAction) {
  try {
    const newMiniProfileCombos = action.payload;
    const { miniSequences } = yield select();
    addSequenceNames(newMiniProfileCombos, miniSequences);
    yield put(actions.updateMiniProfileComboSuccessAction(newMiniProfileCombos));
  } catch (err) {
    log.error(`updateMiniProfileComboSaga error: ${err}`);
  }
}

