export function* fetchSystemTimeSettings(action: FetchSystemTimeRequestAction) {
  try {
    const { callback } = action.payload;
    const response = yield call(api.systemTime);

    yield put(actions.fetchSystemTimeSuccessAction(response));
    if (callback) {
      yield callback();
    }
  } catch (err) {
    log.error(`fetchSystemTimeSettings error: ${err}`);
  }
}

export function* setSystemTimeSettings(action: SetSystemTimeRequestAction) {
  try {
    const { key, value } = action.payload;

    yield call(api.editSystemTime, key, value);

    yield put(actions.fetchSystemTimeRequestAction({}));
  } catch (err) {
    log.error(`setSystemTimeSettings error: ${err}`);
  }
}

export function* setPendLoadingProfileCombo(action: PendLoadingProfileCombo) {
  try {
    const { sequence } = yield select();
    const { sequenceId: pendSequenceId } = action.payload;
    // we do not want to show loadingProfile for sequence profile changes
    if (sequence && sequence.id === pendSequenceId) {
      yield put(actions.pendUpdate(PEND_UPDATE_REASON.LOADING_STEP_PROFILE_COMBO));
    } else {
      yield put(actions.pendUpdate(PEND_UPDATE_REASON.LOADING_PROFILE_COMBO));
    }
  } catch (err) {
    log.error(`setPendLoadingProfileCombo error: ${err}`);
  }
}

let frameImageInterval: (() => void) | null = null;

function startFrameImageInterval() {
  return eventChannel(emitter => {
    const iv = setInterval(() => {
      emitter(true);
    }, 1000);
    frameImageInterval = () => emitter(END);
    // The subscriber must return an unsubscribe function
    return () => {
      clearInterval(iv);
    };
  });
}

export function* setFrameImageTimerSaga(action: SetFrameImageTimer) {
  try {
    const isSetFrameImageTimer = action.payload;
    frameImageInterval && frameImageInterval();
    if (isSetFrameImageTimer) {
      const channel = yield call(startFrameImageInterval);
      try {
        while (true) {
          // take(END) will cause the saga to terminate by jumping to the finally block
          yield take(channel);
          const { ui } = yield select();
          if (
            ui.pendingUpdate === PEND_UPDATE_REASON.LOADING_STEP_PROFILE_COMBO ||
            ui.pendingUpdate === PEND_UPDATE_REASON.LOADING_PROFILE_COMBO ||
            ui.pendingUpdate === PEND_UPDATE_REASON.LOADING_SEQUENCE
          ) {
            yield put(actions.setFrameImageTimer(false));
          } else {
            yield put(actions.updateFrameImageTimer((ui.frameImageTimer || 1000) + 1000));
          }
        }
      } finally {
        frameImageInterval = null;
      }
    } else {
      yield put(actions.updateFrameImageTimer(0));
    }
  } catch (err) {
    log.error(`setPendLoadingProfileCombo error: ${err}`);
  }
}

export default function* rootSaga() {
  yield all([
    takeEvery(FETCH_CAMERAS_REQUEST, fetchCamerasRequestSaga),
    takeEvery(FETCH_FIXI_STATE_REQUEST, fetchFixiStateRequestSaga),
    takeEvery(FETCH_MINI_PROFILE_COMBOS_REQUEST, fetchMiniProfileCombosRequestSaga),
    takeEvery(FETCH_MINI_SEQUENCES_REQUEST, fetchMiniSequencesRequestSaga),
    takeEvery(FETCH_SYSTEM_SETTINGS_REQUEST, fetchSystemSettingsRequestSaga),
    takeEvery(FETCH_PROFILE_SETTINGS_REQUEST, fetchProfileSettingsRequestSaga),
    takeEvery(SET_FAILED_FRAME_AS_FROZEN_FRAME, setFailedFrameAsFrozenFrame),
    takeEvery(FETCH_SYSTEM_TIME_SETTINGS_REQUEST, fetchSystemTimeSettings),
    takeEvery(SET_SYSTEM_TIME_SETTINGS_REQUEST, setSystemTimeSettings),
    takeEvery(PEND_LOADING_PROFILE_COMBO, setPendLoadingProfileCombo),
    takeEvery(UPDATE_MINI_PROFILE_COMBO, updateMiniProfileComboSaga),
    takeEvery(SET_FRAME_IMAGE_TIMER, setFrameImageTimerSaga),
  ]);
}
