import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import { StylesProvider } from '@material-ui/styles';
import React from 'react';
import ReactDOM from 'react-dom';
import { registerObserver } from 'react-perf-devtool';
import { Provider } from 'react-redux';
import registerServiceWorker from './registerServiceWorker';
import { globalReduxStore } from './store/reduxStore';
import App from './ui/App';
import ErrorBoundary from './ui/ErrorBoundary';
import { componentsStyleCatalog, defaults } from './utils/StyleGuide';
import { ApiHost } from './store/hosts';
import log from 'loglevel';
import remote from 'loglevel-plugin-remote';
import LockerDialog from './ui/Common/LockerDialog';
import './index.css';

if (process.env.REACT_APP_METRICS === 'true') {
  const options = {
    shouldLog: true,
    port: 5001,
    components: [
      'SetupScreen',
      'InspectionScreen',
      'WithFramesStore',
      'PolygonDrawerStack',
      'MarkingDataViewer',
      'FrameDrawer',
    ],
  };

  window.observer = registerObserver(options);
}

log.enableAll();

global.critical = false;

function json(log: log.RootLogger): log.RootLogger {
  if (global.critical) {
    (log as any).level = 'critical';
    global.critical = false;
  } else {
    (log as any).level = (log as any).level.label;
  }
  return log;
}

remote.apply(log, {
  url: `${ApiHost}/frontend_logger`,
  stacktrace: {
    levels: ['error'],
    depth: 3,
    excess: 0,
  },
  format: json,
});

const theme = createMuiTheme({
  palette: {
    type: 'light',
  },
  overrides: {
    MuiStepIcon: componentsStyleCatalog.stepLabelIcon,
    MuiTypography: {
      root: defaults.typography,
      body1: defaults.typography,
      body2: defaults.typography,
    },
    DialogTitle: {
      root: defaults.typography,
    },
    DialogContentText: {
      root: defaults.typography,
    },
    TextField: {
      root: defaults.typography,
    },
  },
  typography: defaults.typography,
} as any);

ReactDOM.render(
  <StylesProvider injectFirst>
    <Provider store={globalReduxStore}>
      <MuiThemeProvider theme={theme}>
        <LockerDialog>
          <ErrorBoundary>
            <App />
          </ErrorBoundary>
        </LockerDialog>
      </MuiThemeProvider>
    </Provider>
  </StylesProvider>,
  document.getElementById('root')
);

registerServiceWorker();
