import log from 'loglevel';
import {
  CREATE_FROZEN_FRAME,
  REMOVE_FROZEN_FRAME,
  FrozenFrameActionTypes,
  FrozenFrameState,
} from './types';

const initialFrozenFrameState = null;
export const frozenFrameReducer = (
  state: FrozenFrameState = initialFrozenFrameState,
  action: FrozenFrameActionTypes
): FrozenFrameState => {
  switch (action.type) {
    case CREATE_FROZEN_FRAME:
      log.info(
        'frozenFrame update',
        JSON.stringify(
          {
            frame: Boolean(action.payload.frame),
            polygons: action.payload.polygons,
            status: action.payload.status,
          },
          null,
          4
        )
      );
      if (action.payload) {
        return action.payload;
      }
      return state;
    case REMOVE_FROZEN_FRAME:
      log.info(
        'frozenFrame removed. frozenFrame =',
        JSON.stringify(initialFrozenFrameState)
      );
      return initialFrozenFrameState;
    default:
      return state;
  }
};
