import {
  SET_FAILED_FRAME_AS_FROZEN_FRAME,
  CREATE_FROZEN_FRAME,
  REMOVE_FROZEN_FRAME,
  SetFailedFrameAsFrozenFrame,
  CreateFrozenFrame,
  RemoveFrozenFrame,
  FrozenFrame,
} from './types';

export const setFailedFrameAsFrozenFrame = (): SetFailedFrameAsFrozenFrame => ({
  type: SET_FAILED_FRAME_AS_FROZEN_FRAME,
});

export const createFrozenFrame = ({
  frame,
  polygons,
  status,
}: FrozenFrame): CreateFrozenFrame => ({
  type: CREATE_FROZEN_FRAME,
  payload: {
    frame,
    polygons,
    status,
  },
});

export const removeFrozenFrame = (): RemoveFrozenFrame => ({
  type: REMOVE_FROZEN_FRAME,
});
