import { Part, Station, ReferencesStatus } from '../fixiState/types';
/* =============================================>>>>>
= MINI PROFILE COMBINATIONS =
===============================================>>>>> */
export const FETCH_MINI_PROFILE_COMBOS_REQUEST = 'FETCH_MINI_PROFILE_COMBOS_REQUEST';
export const FETCH_MINI_PROFILE_COMBOS_FAILURE = 'FETCH_MINI_PROFILE_COMBOS_FAILURE';
export const FETCH_MINI_PROFILE_COMBOS_SUCCESS = 'FETCH_MINI_PROFILE_COMBOS_SUCCESS';
export const REMOVE_MINI_PROFILE_COMBO = 'REMOVE_MINI_PROFILE_COMBO';
export const UPDATE_MINI_PROFILE_COMBO = 'UPDATE_MINI_PROFILE_COMBO';
export const UPDATE_MINI_PROFILE_COMBO_SUCCESS = 'UPDATE_MINI_PROFILE_COMBO_SUCCESS';

/* =============================================>>>>>
= MINI SEQUENCES =
===============================================>>>>> */
export const FETCH_MINI_SEQUENCES_REQUEST = 'FETCH_MINI_SEQUENCES_REQUEST';
export const FETCH_MINI_SEQUENCES_FAILURE = 'FETCH_MINI_SEQUENCES_FAILURE';
export const FETCH_MINI_SEQUENCES_SUCCESS = 'FETCH_MINI_SEQUENCES_SUCCESS';
export const UPDATE_MINI_SEQUENCE = 'UPDATE_MINI_SEQUENCE';

export interface MiniProfileCombo {
  profileComboId: string;
  profileId: string;
  profileName: string;
  profilePicture: string;
  connectivityId: number;
  relatedSequences: { id: string; name?: string }[];
  part: Part;
  station: Station;
  referencesStatus: ReferencesStatus;
  isArchived: boolean;
  isFinishedSetup: boolean;
  isReadyForInspection: boolean;
  stage: number;
  width: number;
  height: number;
  creationTime: string;
}

export interface MiniProfileCombosState {
  [id: string]: MiniProfileCombo;
}

export interface MiniSequenceStep {
  stepName: string;
  stepId: string;
  profileComboId: string;
}

export interface MiniSequence {
  id: string;
  name: string;
  isReady: boolean;
  creationTime: string;
  isDraftActive: boolean;
  steps: MiniSequenceStep[];
}

export interface MiniSequencesState {
  [id: string]: MiniSequence;
}

/* =============================================>>>>>
= MINI PROFILE COMBINATIONS =
===============================================>>>>> */
export interface FetchMiniProfileCombosRequestAction {
  type: typeof FETCH_MINI_PROFILE_COMBOS_REQUEST;
  payload: {
    callback: (() => void) | null;
  };
}

export interface FetchMiniProfileCombosSuccessAction {
  type: typeof FETCH_MINI_PROFILE_COMBOS_SUCCESS;
  payload: any;
}

export interface FetchMiniProfileCombosFailedAction {
  type: typeof FETCH_MINI_PROFILE_COMBOS_FAILURE;
  payload: Error;
}

export interface RemoveProfileComboAction {
  type: typeof REMOVE_MINI_PROFILE_COMBO;
  payload: {
    id: string;
  };
}

export interface UpdateMiniProfileComboAction {
  type: typeof UPDATE_MINI_PROFILE_COMBO;
  payload: any;
}

export interface UpdateMiniProfileComboSuccessAction {
  type: typeof UPDATE_MINI_PROFILE_COMBO_SUCCESS;
  payload: any;
}
/* =============================================>>>>>
= MINI SEQUENCES =
===============================================>>>>> */
export interface FetchMiniSequencesRequestAction {
  type: typeof FETCH_MINI_SEQUENCES_REQUEST;
  payload: {
    callback: (() => void) | null;
  };
}

export interface FetchMiniSequencesSuccessAction {
  type: typeof FETCH_MINI_SEQUENCES_SUCCESS;
  payload: any;
}

export interface FetchMiniSequencesFailedAction {
  type: typeof FETCH_MINI_SEQUENCES_FAILURE;
  payload: any;
}

export interface UpdateMiniSequenceAction {
  type: typeof UPDATE_MINI_SEQUENCE;
  payload: any;
}

export type MiniStateActionTypes =
  | FetchMiniProfileCombosRequestAction
  | FetchMiniProfileCombosSuccessAction
  | FetchMiniProfileCombosFailedAction
  | RemoveProfileComboAction
  | UpdateMiniProfileComboAction
  | UpdateMiniProfileComboSuccessAction
  | FetchMiniSequencesRequestAction
  | FetchMiniSequencesSuccessAction
  | FetchMiniSequencesFailedAction
  | UpdateMiniSequenceAction;
