import log from 'loglevel';
import { SET_ERROR, CLEAR_ERROR, SysError, ErrorStateActionTypes } from './types';
import { SYS_ERRORS } from './enums';

export const initialErrorState: SysError = {
  id: '',
  value: '',
  cameraId: '',
};
export const errorReducer = (
  state = initialErrorState,
  action: ErrorStateActionTypes
): SysError => {
  switch (action.type) {
    case SET_ERROR: {
      // In case we got a system error after a camera disconnected error, ignore it
      // and keep showing the camera disconnected error. This is because the backend crash
      // automatically generated a system error occurred message.
      if (
        state.id === SYS_ERRORS.CAMERA_DISCONNECTED &&
        action.payload.id === SYS_ERRORS.SYSTEM_ERROR_OCCURRED
      ) {
        return state;
      }
      log.info('error reducer called', JSON.stringify(action.payload));
      return action.payload;
    }
    case CLEAR_ERROR:
      log.info('error reducer error cleared');
      return initialErrorState;
    default:
      return state;
  }
};
