import { MarkingData } from '../fixiState/types';
export const SET_FAILED_FRAME_AS_FROZEN_FRAME = 'SET_FAILED_FRAME_AS_FROZEN_FRAME';
export const CREATE_FROZEN_FRAME = 'CREATE_FROZEN_FRAME';
export const REMOVE_FROZEN_FRAME = 'REMOVE_FROZEN_FRAME';

export interface FrozenFrame {
  frame: string;
  polygons: MarkingData;
  status: number;
}

export type FrozenFrameState = FrozenFrame | null;

export interface SetFailedFrameAsFrozenFrame {
  type: typeof SET_FAILED_FRAME_AS_FROZEN_FRAME;
}

export interface CreateFrozenFrame {
  type: typeof CREATE_FROZEN_FRAME;
  payload: FrozenFrame;
}

export interface RemoveFrozenFrame {
  type: typeof REMOVE_FROZEN_FRAME;
}

export type FrozenFrameActionTypes =
  | SetFailedFrameAsFrozenFrame
  | CreateFrozenFrame
  | RemoveFrozenFrame;
