import { AnnotatedFrame } from './types';

const framesStore = (() => {
  let state: AnnotatedFrame | {} = {};
  let listeners: ((annotatedFrame: AnnotatedFrame) => void)[] = [];
  // important statuses come from the important main algo queue to make sure they arrive
  // we save important statuses for importantStatusCacheTime
  let stickyState: AnnotatedFrame | null = null;

  const getState = () => state;

  const getStickyState = () => stickyState;

  const setStickyState = (state: AnnotatedFrame) => {
    stickyState = state;
  };

  const removeStickyState = () => {
    stickyState = null;
  };

  const subscribe = (listener: (annotatedFrame: AnnotatedFrame) => void) => {
    listeners.push(listener);
    const unsubscribe = () => {
      listeners = listeners.filter(l => l !== listener);
    };
    return unsubscribe;
  };

  // TODO make newState not mutable. No clash between listeners
  const setState = (newState: AnnotatedFrame) => {
    state = newState;
    if (newState.is_sticky) {
      stickyState = newState;
    }
    listeners.forEach(listener => listener(newState));
  };

  return {
    getState,
    setState,
    subscribe,
    getStickyState,
    setStickyState,
    removeStickyState,
  };
})();

export default framesStore;
