import React from 'react';
import styled from 'styled-components/macro';
import PropTypes from 'prop-types';
import Dialog from '@material-ui/core/Dialog';
import { connect } from 'react-redux';
import { hideWarningAction } from '../../store/actions';
import Warning from './Warning';

const closeIcon = require('../../images/warningClose.png');

// Mui AppBar default z-index is 1100
const zIndexTopBar = 1100;

const WarningDialogBox = styled(({ ...props }) => (
  <Dialog classes={{ paper: 'paper' }} {...props} />
))`
  .paper {
    position: absolute;
    min-width: 692px;
    height: auto;
    min-height: 400px;
    margin: 0;
    left: ${props =>
      props.bounds ? `${props.bounds.x + props.bounds.width / 2}px` : '50%'};
    top: ${props =>
      props.bounds ? `${props.bounds.y + props.bounds.height / 2}px` : '50%'};
    z-index: ${zIndexTopBar + 10};
    transform: translate(-50%, -50%);
    border-radius: 8px;
    box-shadow: 0 3px 16px 0 rgba(0, 0, 0, 0.52);
  }
`;

const CloseButton = styled.img`
  position: absolute;
  width: 24px;
  height: 24px;
  top: 28px;
  right: 28px;
  cursor: pointer;
`;

const WarningDialog = ({
  buttons,
  open,
  icon,
  text,
  iconColor,
  title,
  targetBounds,
  hideWarningAction,
  hasClose,
}) => (
  <WarningDialogBox
    open={open}
    bounds={targetBounds}
    onClose={hasClose ? hideWarningAction : null}
  >
    <Warning
      buttons={buttons}
      open={open}
      icon={icon}
      text={text}
      iconColor={iconColor}
      title={title}
      hideWarningAction={hideWarningAction}
    />
    {hasClose ? (
      <CloseButton src={closeIcon} onClick={hideWarningAction} alt="" draggable={false} />
    ) : null}
  </WarningDialogBox>
);
WarningDialog.propTypes = {
  buttons: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      name: PropTypes.string,
      toolTip: PropTypes.string,
    })
  ),
  icon: PropTypes.string,
  iconColor: PropTypes.string,
  text: PropTypes.oneOfType([PropTypes.string, PropTypes.shape({})]),
  title: PropTypes.string,
  open: PropTypes.bool,
  targetBounds: PropTypes.shape({}),
  hasClose: PropTypes.bool,
};
WarningDialog.defaultProps = {
  buttons: [],
  icon: null,
  text: null,
  open: false,
  iconColor: null,
  title: null,
  targetBounds: null,
  hasClose: false,
};
const mapStateToProps = () => ({});

export default connect(mapStateToProps, {
  hideWarningAction,
})(WarningDialog);
