import Dialog from '@material-ui/core/Dialog';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components/macro';
import { ColorPalette } from '../../utils/StyleGuide';
import { useSelector } from 'react-redux';
import { RootStore } from '../../store/reduxStore';

const iconLogo = require('../../images/Completed.svg');

const StyledDialog = styled(props => {
  const { fromInspection, ...rest } = props;
  return <Dialog classes={{ paper: 'paper' }} {...rest} />;
})`
  .paper {
    width: ${props => props.size.width}px;
    height: ${props => props.size.height}px;
    max-width: 10000px;
    max-height: 10000px;
  }
`;

const Label = styled(({ ...props }) => <Typography {...props} />)`
  font-size: 40px;
  text-align: center;
  transform: translateX(-50%);
  position: absolute;
  top: 77px;
  white-space: nowrap;
  color: ${ColorPalette.passed};
`;

const Content = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
`;

const Container = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
`;

const Image = styled.img`
  transform: translate(-50%, -86%);
  width: 170px;
  height: 170px;
`;

interface Props {
  open: boolean;
  label: string;
}
const CompleteDialog = ({ open, label }: Props) => {
  const { size } = useSelector((state: RootStore) => ({
    size: state.profileValidationSize,
  }));
  return (
    <StyledDialog open={open} size={size}>
      <Content>
        <Container>
          <Image src={iconLogo} alt="Check Logo" />
          <Label>{label}</Label>
        </Container>
      </Content>
    </StyledDialog>
  );
};

CompleteDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  label: PropTypes.string.isRequired,
};

export default CompleteDialog;
