import _ from 'lodash';
import {
  INSPECTION_TRIGGER_MODE_NAME,
  POSITION_DISPLAY_MODE,
  PROFILE_STAGES,
  STEP_ACTIONS,
  SEQUENCE_PRESETS,
  FRAME_STATUS,
} from './enums';
export const FETCH_FIXI_STATE_REQUEST = 'FETCH_FIXI_STATE_REQUEST';
export const FETCH_FIXI_STATE_FAILURE = 'FETCH_FIXI_STATE_FAILURE';
export const SET_ACTIVE_PROFILE_COMBO = 'SET_ACTIVE_PROFILE_COMBO';
export const SET_ACTIVE_SEQUENCE = 'SET_ACTIVE_SEQUENCE';

export interface Plant {
  customer: {
    id: string;
    name: string;
  };
}
export interface Station {
  id: string;
  name: string;
  description: string;
  productionLine: {
    id: string;
    name: string;
    plant: Plant;
  };
  lastAutoGeneratedImageNameInt: number | null;
}

export interface Part {
  id: string;
  name: string;
  modelName: string;
}

export interface ReferencesStatus {
  progressStatus: number;
  givenReferences: number;
  requiredReferences: number;
}

export type PolyPoint = [number, number];

export type Polygon = PolyPoint[];

export interface Polygons {
  outline: Polygon;
  holes: Polygon[];
  is_valid?: boolean;
  [any: string]: any;
}

export interface MarkingData {
  item: Polygons;
  rois: Polygons[];
  defects: Polygons[];
}

export interface RoiPolygonMetadata {
  name: string;
  isNegative: boolean;
}

export interface MarkingMetadata {
  rois: RoiPolygonMetadata[];
}

export interface ReferenceCandidateFromVideo {
  barcodesList: [string];
  cameraFrameTime: string;
  cameraId: string;
  frameStatus: FRAME_STATUS;
  imageId: string | null;
  markingData: MarkingData;
  profileId: string;
}

export interface StageMarkers {
  referenceCandidateFromInspectionId: string | null;
  isFinishedSetup: boolean;
  curScreen: number;
  referenceCandidateFromVideoImageStr: string | null;
  referenceCandidateFromVideo: ReferenceCandidateFromVideo | null;
  referenceCandidateFromInspectionImageStr: string | null;
  editedMarkingData: MarkingData | null;
  editedReference: any | null;
  markingMetadata: MarkingMetadata;
}

export interface ProfileSettings {
  positionDisplayMode: POSITION_DISPLAY_MODE;
  noveltyDetectionThresholdPercentage: number;
  noveltyFlowMeasurementThresholdPx: number;
  movementDetectionHysteresisMs: number;
  timeThresholdAfterMovingOutMs: number;
  minProfileReferencesNum: number;
  minPositionReferencesNum: number;
  allowRuntimeAgc: boolean;
  inspectionTriggerModeName: INSPECTION_TRIGGER_MODE_NAME;
  minimalDefectArea: number;
}

export interface Batch {
  id: string;
  inspectionEnabledDurationSeconds: number;
  name: string | null;
  profileComboId: string | null;
  sequenceId: string | null;
  startTime: string;
}

export interface Profile {
  id: string;
  name: string | null;
  part: Part | null;
  cameraId: string;
  stageMarkers: StageMarkers;
  setZoom: number;
  settings: ProfileSettings;
  isHdr: boolean | null;
  stage: PROFILE_STAGES;
  referencesStatus: ReferencesStatus;
  areReferencesValidated: boolean;
}

export type Profiles = Profile[];

export interface ProfileCombo {
  id: string;
  name: string | null;
  station: Station | null;
  isArchived: boolean;
  connectivityId: number;
  wasUnloaded: boolean;
  productionLine: string | null;
  stationDescription: string | null;
  batch: Batch;
  lastAutoGeneratedImageName?: string;
  physicalLocation: string | null;
  profiles: Profiles;
}

export const isProfileComboNonEmpty = (
  state: ProfileComboState
): state is ProfileCombo => {
  return !_.isEmpty(state);
};

export type ProfileComboState = ProfileCombo | {};

export interface SequenceStep {
  profileComboId: string;
  name: string;
  onNok: STEP_ACTIONS;
  onAlert: STEP_ACTIONS;
}

export interface BaseSequence {
  id: string;
  name: string | null;
  steps: SequenceStep[];
  stepInterval: number;
  preset: SEQUENCE_PRESETS;
  isReady: boolean;
}

export interface DraftSequence {
  id: string;
  name: string | null;
  steps: SequenceStep[];
  stepInterval: number;
  preset: SEQUENCE_PRESETS;
  isReady: boolean;
  isActive: boolean;
}
export interface Sequence {
  id: string;
  name: string | null;
  steps: SequenceStep[];
  stepInterval: number;
  preset: SEQUENCE_PRESETS;
  isReady: boolean;
  draft: DraftSequence;
  activeStep: number;
  isArchived: boolean;
  isFinishedSetup: boolean;
  isCreateNewBatchOnSave: boolean;
  wasUnloaded: boolean;
  batch: Batch;
}

export type SequenceState = Sequence | null;

export interface FixiState {
  activeProfileCombo: ProfileComboState | null;
  activeSequence: SequenceState;
  id: string;
  lastAutoGeneratedImageName: string;
  wasUnloaded: boolean;
}

export interface FetchFixiStateRequestAction {
  type: typeof FETCH_FIXI_STATE_REQUEST;
  payload: { callback: (() => void) | null; fixiState: any };
}

export interface FetchFixiStateFailedAction {
  type: typeof FETCH_FIXI_STATE_FAILURE;
  payload: Error;
}

export interface SetActiveSequenceAction {
  type: typeof SET_ACTIVE_SEQUENCE;
  payload: SequenceState;
}

export interface SetActiveProfileComboAction {
  type: typeof SET_ACTIVE_PROFILE_COMBO;
  payload: ProfileComboState;
}

export type FixiStateActionTypes =
  | FetchFixiStateRequestAction
  | FetchFixiStateFailedAction
  | SetActiveSequenceAction
  | SetActiveProfileComboAction;
