import {
  FETCH_FIXI_STATE_REQUEST,
  FETCH_FIXI_STATE_FAILURE,
  SET_ACTIVE_SEQUENCE,
  SET_ACTIVE_PROFILE_COMBO,
  FetchFixiStateRequestAction,
  FetchFixiStateFailedAction,
  SetActiveSequenceAction,
  SetActiveProfileComboAction,
  SequenceState,
  ProfileComboState,
  FixiState,
} from './types';

/* =============================================>>>>>
= FIXI_STATE =
===============================================>>>>> */
export const fetchFixiStateRequestAction = (data: {
  callback?: (() => void) | null;
  fixiState?: FixiState;
}): FetchFixiStateRequestAction => {
  const { callback = null, fixiState } = data || {};
  return {
    type: FETCH_FIXI_STATE_REQUEST,
    payload: { callback, fixiState },
  };
};

export const fetchFixiStateFailedAction = (error: Error): FetchFixiStateFailedAction => ({
  type: FETCH_FIXI_STATE_FAILURE,
  payload: error,
});

/* =============================================>>>>>
= ACTIVE SEQUENCE =
===============================================>>>>> */
export const setActiveSequenceAction = (
  sequence: SequenceState
): SetActiveSequenceAction => ({
  type: SET_ACTIVE_SEQUENCE,
  payload: sequence,
});

/* =============================================>>>>>
= ACTIVE PROFILE_COMBO =
===============================================>>>>> */
export const setActiveProfileComboAction = (
  profileCombo: ProfileComboState
): SetActiveProfileComboAction => ({
  type: SET_ACTIVE_PROFILE_COMBO,
  payload: profileCombo,
});
