import {
  PASSWORD_PROMPT_VISIBLE,
  PasswordPromptState,
  PasswordPromptVisibleAction,
} from './types';
export const initialPasswordPromptState: PasswordPromptState = {
  isVisible: false,
};
export const passwordPromptReducer = (
  state = initialPasswordPromptState,
  action: PasswordPromptVisibleAction
): PasswordPromptState => {
  // eslint-disable-line
  switch (action.type) {
    case PASSWORD_PROMPT_VISIBLE: {
      return {
        ...state,
        isVisible: action.payload || false,
      };
    }
    default:
      return state;
  }
};
