import log from 'loglevel';
import _ from 'lodash';
import {
  FETCH_FIXI_STATE_FAILURE,
  SET_ACTIVE_SEQUENCE,
  SET_ACTIVE_PROFILE_COMBO,
  FixiStateActionTypes,
  SetActiveSequenceAction,
  ProfileComboState,
  SequenceState,
  isProfileComboNonEmpty,
} from './types';
import { PROFILE_STAGES } from './enums';
import { difference, removeKeysDeep } from '../../utils/Utils';

// TODO(ROIP) make profileCombo and sequence have the same structure where u can
// so we will not have to check isSequence all the time
const initialProfileComboState: ProfileComboState = {};
export const profileComboReducer = (
  state: ProfileComboState = initialProfileComboState,
  action: FixiStateActionTypes
): ProfileComboState => {
  switch (action.type) {
    case SET_ACTIVE_PROFILE_COMBO:
      // NOTE: For Test Automation purposes.
      if (process.env.NODE_ENV === 'development') {
        window.currentProfileCombo = action.payload;
      }

      log.info(
        'profile combo update',
        JSON.stringify(
          difference(
            removeKeysDeep(action.payload, [
              'referenceCandidateFromVideoImageStr',
              'referenceCandidateFromInspectionImageStr',
            ]),
            removeKeysDeep(state, [
              'referenceCandidateFromVideoImageStr',
              'referenceCandidateFromInspectionImageStr',
            ])
          ),
          null,
          4
        )
      );
      if (
        isProfileComboNonEmpty(state) &&
        !_.isEmpty(state.profiles) &&
        isProfileComboNonEmpty(action.payload) &&
        !_.isEmpty(action.payload.profiles)
      ) {
        const { stage: prevStage } = state.profiles[0];
        const { stage } = action.payload.profiles[0];
        if (prevStage !== stage) {
          log.info('profile combo stage updated', PROFILE_STAGES[stage]);
        }
      }
      if (action.payload) {
        return action.payload;
      }
      return state;
    case FETCH_FIXI_STATE_FAILURE:
      log.error(`fetchActiveFixiStateRequestSaga error: ${action.payload}`);
      return state;
    default:
      return state;
  }
};

const initialSequence: SequenceState = null;
export const sequenceReducer = (
  state = initialSequence,
  action: SetActiveSequenceAction
): SequenceState => {
  switch (action.type) {
    case SET_ACTIVE_SEQUENCE:
      return action.payload;
    default:
      return state;
  }
};
