import { applyMiddleware, combineReducers, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga';
import { camerasReducer } from './cameras/reducers';
import { uiReducer } from './ui/reducers';
import { profileComboReducer, sequenceReducer } from './fixiState/reducers';
import {
  allMiniSequencesReducer,
  allMiniProfileCombosReducer,
} from './miniState/reducers';
import { frozenFrameReducer } from './frozenFrame/reducers';
import { settingsReducer } from './settings/reducers';
import { systemTimeSettingsReducer } from './systemTime/reducers';
import { errorReducer } from './errors/reducers';
import { warningReducer } from './warnings/reducers';
import { passwordPromptReducer } from './passwordPrompt/reducers';
import { profileValidationSize } from './profileValidationSize/reducers';
import rootSaga from './saga';

const rootReducer = combineReducers({
  ui: uiReducer,
  cameras: camerasReducer,
  profileCombo: profileComboReducer,
  sequence: sequenceReducer,
  miniProfileCombos: allMiniProfileCombosReducer,
  miniSequences: allMiniSequencesReducer,
  frozenFrame: frozenFrameReducer,
  settings: settingsReducer,
  systemTime: systemTimeSettingsReducer,
  error: errorReducer,
  warning: warningReducer,
  passwordPrompt: passwordPromptReducer,
  profileValidationSize: profileValidationSize,
});

export type RootStore = ReturnType<typeof rootReducer>;

const configureStore = () => {
  const sagaMiddleware = createSagaMiddleware();
  const reduxStore = createStore(
    rootReducer,
    composeWithDevTools(applyMiddleware(sagaMiddleware))
  );
  sagaMiddleware.run(rootSaga);
  return reduxStore;
};

export const globalReduxStore = configureStore();

export type AppDispatch = typeof globalReduxStore.dispatch;
