import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core';
import OtpInput from 'react-otp-input';
import { ColorPalette } from '../utils/StyleGuide';

const styles = {
  dialog: {
    padding: '48px',
  },
  message: {
    fontSize: '22px',
    marginBottom: '7px',
    color: ColorPalette.black,
  },
  title: {
    marginTop: 0,
    marginBottom: '12px',
    textAlign: 'center',
    fontSize: '20px',
    fontWeight: 'bold',
    color: ColorPalette.darkSkyBlue,
  },
  okButton: {
    marginLeft: 0,
    maxHeight: '10%',
    borderRadius: 24,
    borderWidth: 2,
    borderStyle: 'solid',
    borderColor: ColorPalette.darkSkyBlue,
    backgroundColor: ColorPalette.darkSkyBlue,
    width: 132,
    height: 39,
    fontSize: 22,
    lineHeight: 1.23,
    textTransform: 'capitalize',
    color: ColorPalette.white,
    padding: 0,
    minHeight: 0,
  },
  cancelButton: {
    marginRight: '60px',
    maxHeight: '10%',
    borderRadius: 24,
    borderWidth: 2,
    borderStyle: 'solid',
    backgroundColor: ColorPalette.white,
    width: 132,
    height: 39,
    fontSize: 22,
    lineHeight: 1.23,
    textTransform: 'capitalize',
    color: ColorPalette.darkSkyBlue,
    padding: 0,
    minHeight: 0,
  },
  dialogActions: {
    justifyContent: 'center',
    marginTop: '12px',
  },
  cssLabel: {
    '&$cssFocused': {
      color: ColorPalette.darkSkyBlue,
    },
  },
  cssFocused: {
    color: '#2ba8e0 !important',
  },
  inputField: {
    paddingLeft: '8px',
  },
  notchedOutline: {
    '&:before': {
      borderBottom: '1px solid #2ba8e0 !important',
    },
    '&:after': {
      borderBottom: '2px solid #2ba8e0 !important',
    },
  },
};

const SystemTime = props => {
  if (props.visible) {
    return (
      <>
        <p style={{ fontWeight: 'bold', textAlign: 'center', marginBottom: 5 }}>
          Is this the current time?
        </p>
        <p style={{ textAlign: 'center', fontSize: 12 }}>
          {Date().toLocaleString()}
          <br />
          Timestamp: {Math.floor(Date.now() / 1000)}
        </p>
      </>
    );
  } else {
    return null;
  }
};

const ErrorMessage = props => {
  if (props.visible) {
    return (
      <>
        <p style={{ color: 'red', textAlign: 'center' }}>Incorrect Password</p>
      </>
    );
  } else {
    return null;
  }
};

class OTPDialog extends React.Component {
  state = {
    otp: '',
  };

  handleChange = otp => this.setState({ otp });

  handle = e => {
    if (e.nativeEvent instanceof KeyboardEvent) {
      if (e.key === 'Enter') {
        this.submit();
      }
    }
  };

  submit = () => {
    const { handleSend } = this.props;
    const { otp } = this.state;
    handleSend(otp);
    this.setState({ otp: '' });
  };

  render() {
    const {
      handleClose,
      title,
      classes,
      open,
      hasErrored,
      showTime,
      noPassword,
    } = this.props;

    const { otp } = this.state;
    return (
      <div>
        <Dialog
          onKeyPress={this.handle}
          open={open}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
          id="form-dialog-password-prompt"
          classes={{ paperWidthSm: classes.dialog }}
        >
          <DialogTitle id="form-dialog-title" style={{ padding: 0, margin: 0 }}>
            <Typography classes={{ root: classes.title }}>
              {noPassword ? 'Lock System?' : title}
            </Typography>
          </DialogTitle>
          <ErrorMessage visible={hasErrored} />
          <SystemTime visible={showTime} />
          <DialogContent style={{ padding: 0, marginTop: '12px', marginBottom: '12px' }}>
            {!noPassword && (
              <OtpInput
                className="false"
                value={otp}
                onChange={this.handleChange}
                numInputs={6}
                containerStyle={{ justifyContent: 'center' }}
                separator={<span>{'\u00A0\u00A0'}</span>}
                inputStyle={{
                  fontSize: '32px',
                  borderRadius: '8px',
                  border: '1px solid lightgray',
                }}
                shouldAutoFocus
              />
            )}
          </DialogContent>
          <DialogActions className={classes.dialogActions}>
            <input type="text" hidden />
            <Button onClick={handleClose} className={classes.cancelButton}>
              Cancel
            </Button>
            <Button
              onClick={this.submit}
              type="submit"
              id="submitPass"
              className={classes.okButton}
            >
              OK
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

OTPDialog.propTypes = {
  classes: PropTypes.shape({}).isRequired,
  handleClose: PropTypes.func.isRequired,
  handleSend: PropTypes.func.isRequired,
  title: PropTypes.string,
  open: PropTypes.bool,
  showTime: PropTypes.bool,
};

OTPDialog.defaultProps = {
  title: 'One Time Password',
  open: true,
};

export default withStyles(styles)(OTPDialog);
