import PropTypes from 'prop-types';
import React from 'react';
import styled, { keyframes } from 'styled-components/macro';
import { ColorPalette } from '../../utils/StyleGuide';

const animLength = 1.8;
const animStart = 28;
const animEnd = 72;
const centerEase = 'cubic-bezier(0.44, .01, 0.66, 1)';

// *** Animations start

const drawArch = turn => keyframes`
    0% {
      stroke-dashoffset: ${turn * 2};
    }
    ${`${animStart}%`} {
      stroke-dashoffset: ${turn * 1};
    }
    ${`${animEnd}%`} {
      stroke-dashoffset: ${turn * 1};
    }
    100% {
      stroke-dashoffset: ${0};
    }
`;

const colorStroke = () => keyframes`
    0% {
      stroke: ${ColorPalette.solidRed};
    }
    ${`${animStart}%`} {
      stroke: ${ColorPalette.deepGrey};
    }
    ${`${animEnd}%`} {
      stroke: ${ColorPalette.deepGrey};
    }
    100% {
      stroke: ${ColorPalette.solidRed};
    }
`;

const colorCircleIn = () => keyframes`
    0% {
      fill: ${ColorPalette.deepGrey};
    }
     ${`${animStart}%`} {
      fill: ${ColorPalette.deepGrey};
    }
    ${`${animEnd}%`} {
      fill: ${ColorPalette.solidRed};
    }
    100% {
      fill: ${ColorPalette.solidRed};
    }
`;

const moveSvgCircles = (left, width, move) => keyframes`
    0% {
      cx: ${width / 2};
    }
    20% {
      cx: ${width / 2};
    }
    80% {
      cx: ${width / 2 + move};
    }
    100% {
      cx: ${width / 2 + move};
    }
`;

const fadeIn = () => keyframes`
    0%   {opacity: 0; }
    100% {opacity: 1; }
`;

// *** Animations over

const CenteredContainer = styled.div`
  position: absolute;
  top: 160px;
  animation: ${() => fadeIn()} 1.8s cubic-bezier(0.6, 0.01, 0.6, 0.99)
    ${props => props.delay}s;
  animation-fill-mode: backwards;
`;

const CircleHolder = styled.div`
  position: absolute;
  width: ${props => props.origin * 2}px;
  transform: translate(${props => props.left}px, ${props => props.top}px);
`;

const MainCircles = styled.div`
  position: absolute;
  width: ${props => props.width}px;
  height: ${props => props.height}px;
  transform: translate(-50%, -50%);
  overflow: hidden;
`;

const CenterCircle = styled.svg`
  position: absolute;
  left: ${props => -props.width / 2}px;
  top: ${props => -props.width / 2}px;
  transform: rotate(-90deg);
  animation: ${props => drawArch(props.width * Math.PI)} ${animLength}s ${centerEase}
    ${props => props.delay}s;
  animation-iteration-count: infinite;
  stroke-dasharray: ${props => props.width * Math.PI};
  stroke-linecap: round;
  & .path {
    animation: ${() => colorStroke()} ${animLength}s ${centerEase}
      ${props => props.delay}s;
    animation-iteration-count: infinite;
  }
`;

const getCircleAnimation = fade => {
  if (fade) {
    return colorCircleIn();
  }
  return 'none';
};

const Circle = styled.svg`
  position: absolute;
  left: ${props => props.left - props.width / 2}px;
  top: ${props => props.top - props.height / 2}px;
  width: ${props => props.width}px;
  height: ${props => props.height}px;
  & .path {
    fill: ${props => (props.colored ? ColorPalette.solidRed : ColorPalette.deepGrey)};
    animation: ${props => getCircleAnimation(props.colorFade)} ${animLength}s
        ${centerEase} ${props => props.delay}s,
      ${props => moveSvgCircles(props.left, props.width, props.move)} ${animLength}s
        cubic-bezier(0.6, 0, 0.4, 1) ${props => props.delay}s;
    animation-iteration-count: infinite;
    animation-fill-mode: backwards;
  }
`;

const Gradient = styled.div`
  position: absolute;
  width: ${props => props.width}px;
  height: ${props => props.height}px;
  left: ${props => props.width / -2}px;
  top: ${props => props.height / -2}px;
  background: linear-gradient(
    to right,
    rgb(
        ${props => props.color[0]},
        ${props => props.color[1]},
        ${props => props.color[2]}
      )
      ${props => props.start}%,
    rgba(
        ${props => props.color[0]},
        ${props => props.color[1]},
        ${props => props.color[2]},
        0
      )
      ${props => props.end}%,
    rgba(
        ${props => props.color[0]},
        ${props => props.color[1]},
        ${props => props.color[2]},
        0
      )
      ${props => 100 - props.end}%,
    rgb(
        ${props => props.color[0]},
        ${props => props.color[1]},
        ${props => props.color[2]}
      )
      ${props => 100 - props.start}%
  );
`;

const LoadingAnimation = ({
  width,
  overflow,
  circle,
  gap,
  center,
  fadeDelay,
  animationDelay,
  backgroundRGB,
}) => {
  const fullWidth = width + width * (overflow * 2);
  const circleNum = Math.ceil(fullWidth / gap) + 2;
  const midCircle = Math.floor(circleNum / 2);
  const origin = midCircle * gap;
  return (
    <CenteredContainer delay={fadeDelay}>
      <MainCircles width={fullWidth} height={center}>
        <CircleHolder left={fullWidth / 2 - origin} top={0} origin={origin}>
          {Array.apply(null, Array(circleNum)).map((curr, i) => (
            <Circle
              key={`circle${i}`}
              width={6 * circle + gap}
              height={circle}
              left={gap * i}
              top={center / 2}
              move={gap}
              colorFade={i === midCircle}
              colored={i > midCircle}
              delay={animationDelay}
            >
              <circle
                className="path"
                cx={circle / 2}
                cy={circle / 2}
                r={circle / 2}
                stroke="none"
                fill="blue"
              />
            </Circle>
          ))}
        </CircleHolder>
      </MainCircles>
      <CenterCircle width={center} height={center} delay={animationDelay}>
        <circle
          className="path"
          cx={center / 2}
          cy={center / 2}
          r={center / 2 - 2}
          stroke="blue"
          strokeWidth="2"
          fill="none"
        />
      </CenterCircle>
      <Gradient
        width={fullWidth}
        height={center}
        start={0}
        end={overflow * 100}
        color={backgroundRGB}
      />
    </CenteredContainer>
  );
};

LoadingAnimation.propTypes = {
  width: PropTypes.number,
  overflow: PropTypes.number,
  circle: PropTypes.number,
  gap: PropTypes.number,
  center: PropTypes.number,
  fadeDelay: PropTypes.number,
  animationDelay: PropTypes.number,
  backgroundRGB: PropTypes.arrayOf(PropTypes.number),
};

LoadingAnimation.defaultProps = {
  width: 300,
  overflow: 0.3, // percent
  circle: 17,
  gap: 70,
  center: 48,
  fadeDelay: 2,
  animationDelay: 1.1,
  backgroundRGB: [255, 255, 255],
};

export default LoadingAnimation;
