import _ from 'lodash';
import moment from 'moment';
import {
  FETCH_MINI_PROFILE_COMBOS_SUCCESS,
  REMOVE_MINI_PROFILE_COMBO,
  UPDATE_MINI_PROFILE_COMBO_SUCCESS,
  FETCH_MINI_SEQUENCES_SUCCESS,
  UPDATE_MINI_SEQUENCE,
  MiniStateActionTypes,
  MiniProfileCombosState,
  MiniSequencesState,
  MiniProfileCombo,
  MiniSequence,
} from './types';
import { serverDateFormat } from '../../utils/Utils';

const sortMinis = ({
  minis,
  id,
  filterFunc = () => true,
}: {
  minis: MiniProfileCombosState | MiniSequencesState;
  id: string;
  filterFunc: (predicate: any) => boolean;
}): MiniProfileCombosState | MiniSequencesState => {
  const getDate = ({ creationTime }: MiniProfileCombo | MiniSequence) =>
    moment(creationTime, serverDateFormat).unix();

  // Take out profiles without name
  const filteredArray = Object.keys(minis)
    .map(key => minis[key])
    .filter(item => filterFunc(item));

  // order by date the created array
  const orderedByDate = filteredArray.sort((a, b) => (getDate(a) > getDate(b) ? -1 : 1));

  // create a reorganized object
  const reorganizedObject: MiniProfileCombosState | MiniSequencesState = {};
  orderedByDate.forEach((curr: any) => {
    reorganizedObject[curr[id]] = curr;
  });
  return reorganizedObject;
};

const sortMiniProfileCombos = (minis: MiniProfileCombosState): MiniProfileCombosState =>
  sortMinis({
    minis,
    id: 'profileComboId',
    filterFunc: (p: MiniProfileCombo) => Boolean(p.profileName),
  }) as MiniProfileCombosState;

export const initialMiniProfileCombosState: MiniProfileCombosState = {};
export const allMiniProfileCombosReducer = (
  state = initialMiniProfileCombosState,
  action: MiniStateActionTypes
): MiniProfileCombosState => {
  // eslint-disable-line
  switch (action.type) {
    case FETCH_MINI_PROFILE_COMBOS_SUCCESS: {
      return {
        ...sortMiniProfileCombos(action.payload),
      };
    }
    case REMOVE_MINI_PROFILE_COMBO: {
      let newMiniProfileCombos = _.cloneDeep(state);
      delete newMiniProfileCombos[action.payload.id];
      return {
        ...sortMiniProfileCombos(newMiniProfileCombos),
      };
    }
    case UPDATE_MINI_PROFILE_COMBO_SUCCESS: {
      let originalMiniProfileCombos = _.cloneDeep(state);
      /*Object.keys(action.payload).forEach(key => {
        delete newMiniProfileCombos[key];
      });
      delete newMiniProfileCombos[action.payload.id];*/
      return {
        ...sortMiniProfileCombos({ ...originalMiniProfileCombos, ...action.payload }),
      };
    }
    default:
      return state;
  }
};

export const initialMiniSequencesState: MiniSequencesState = {};
export const allMiniSequencesReducer = (
  state = initialMiniSequencesState,
  action: MiniStateActionTypes
): MiniSequencesState => {
  // eslint-disable-line
  switch (action.type) {
    case FETCH_MINI_SEQUENCES_SUCCESS: {
      return {
        ...(sortMinis({
          minis: action.payload,
          id: 'id',
          filterFunc: () => true,
        }) as MiniSequencesState),
      };
    }
    case UPDATE_MINI_SEQUENCE: {
      let newMiniSequences = _.cloneDeep(state);
      Object.keys(action.payload).forEach(key => {
        delete newMiniSequences[key];
        // if we removed a sequence we get null under the key
        if (!action.payload[key]) {
          delete action.payload[key];
        }
      });
      return {
        ...(sortMinis({
          minis: { ...action.payload, ...newMiniSequences },
          id: 'id',
          filterFunc: () => true,
        }) as MiniSequencesState),
      };
    }
    default:
      return state;
  }
};
