import React from 'react';
import styled from 'styled-components/macro';
import PropTypes from 'prop-types';
import moment from 'moment';
import { ColorPalette } from '../../utils/StyleGuide';
import { useSelector } from 'react-redux';

export const frameFadeDuration = 2000;

const charWidth = 56;
const charWidthSmall = 28;
const characterAdjustment = 2;
const startX = 2 * charWidth + 0.5 * charWidthSmall + characterAdjustment;
const fontSize = 84;

const ClockWrapper = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: ${props => (props.show ? 1 : 0)};
  transition: ${props =>
    props.show ? 'opacity 1400ms cubic-bezier(0.4, 0, 0.2, 1)' : 'none'};
  pointer-events: none;
  background-color: ${ColorPalette.blackOpaque};
`;

const Clock = styled.div`
  position: absolute;
  left: calc(50% - ${startX}px);
  top: calc(50% - ${fontSize / 2}px);
`;

const ClockCharacter = styled.div`
  position: absolute;
  font-size: ${fontSize}px;
  color: ${ColorPalette.white};
  left: ${props => props.pos}px;
  top: 0;
  width: ${props => (props.short ? charWidthSmall : charWidth)}px;
  font-weight: bold;
  text-align: center;
  text-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
`;

const getDisplayedTimeCharacters = millisecond => {
  const rawTime = moment.duration(millisecond);
  const rawSeconds = rawTime
    .seconds()
    .toString()
    .split('');
  const seconds = rawSeconds.length === 1 ? ['0', rawSeconds[0]] : rawSeconds;
  const rawMinutes = rawTime
    .minutes()
    .toString()
    .split('');
  const minutes = rawMinutes.length === 1 ? ['0', rawMinutes[0]] : rawMinutes;
  let chars = [];
  let posX = 0;

  minutes.forEach(char => {
    chars.push({ char, short: false, x: posX });
    posX += charWidth;
  });

  chars.push({ char: ':', short: true, x: posX });
  posX += charWidthSmall;

  seconds.forEach(char => {
    chars.push({ char, short: false, x: posX });
    posX += charWidth;
  });

  return chars;
};

const InspectionClock = () => {
  const { frameImageTimer } = useSelector(({ ui }) => ({
    frameImageTimer: ui.frameImageTimer,
  }));
  const timeCharacters = frameImageTimer
    ? getDisplayedTimeCharacters(frameImageTimer)
    : [];

  return (
    <ClockWrapper show={Boolean(frameImageTimer)}>
      <Clock>
        {timeCharacters.map((curr, i) => {
          return (
            <ClockCharacter short={curr.short} pos={curr.x} key={`clock-${i}`}>
              {curr.char}
            </ClockCharacter>
          );
        })}
      </Clock>
    </ClockWrapper>
  );
};

InspectionClock.propTypes = {
  show: PropTypes.bool,
};
InspectionClock.defaultProps = {
  show: true,
};
export default InspectionClock;
