import React, { useMemo } from 'react';
import styled from 'styled-components/macro';
import PropTypes from 'prop-types';
import { ColorPalette, symmetricAnimation } from '../../utils/StyleGuide';

const Icon = styled.div`
  width: ${props => props.width};
  height: ${props => props.height};
  background-color: ${props => (props.color ? props.color : 'none')};
  border-radius: ${props => props.radius};
  mask-image: ${props => (props.color ? `url(${props.icon})` : 'none')};
  mask-size: ${props => props.width} ${props => props.height};
  mask-repeat: no-repeat;
  background-image: ${props => (props.color ? 'none' : `url(${props.icon})`)};
  background-size: ${props => props.width} ${props => props.height};
  background-repeat: no-repeat;
  opacity: ${props => (props.show ? 1 : 0)};
  pointer-events: ${props => (!props.interactive || !props.show ? 'none' : 'auto')};
  transition: opacity ${props => (props.animated ? symmetricAnimation : '0s')},
    background-color ${props => (props.animated ? symmetricAnimation : '0s')};
`;

const ColorIcon = ({
  show,
  icon,
  width,
  height,
  noColor,
  color,
  animated,
  borderRadius,
  mouseEvents,
  styleOverride,
}) =>
  useMemo(
    () => (
      <Icon
        show={show}
        icon={icon}
        color={noColor ? null : color}
        width={width}
        height={height}
        interactive={mouseEvents}
        animated={animated}
        radius={borderRadius}
        style={styleOverride}
      />
    ),
    [
      show,
      icon,
      width,
      height,
      noColor,
      color,
      borderRadius,
      mouseEvents,
      styleOverride,
      animated,
    ]
  );

ColorIcon.propTypes = {
  show: PropTypes.bool,
  icon: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  noColor: PropTypes.bool,
  color: PropTypes.string,
  animated: PropTypes.bool,
  background: PropTypes.string,
  borderRadius: PropTypes.string,
  mouseEvents: PropTypes.bool,
  styleOverride: PropTypes.shape({}),
};

ColorIcon.defaultProps = {
  show: true,
  icon: null,
  width: 'auto',
  height: 'auto',
  noColor: false,
  color: ColorPalette.black,
  animated: true,
  background: 'none',
  borderRadius: '0',
  mouseEvents: true,
  styleOverride: {},
};
export default ColorIcon;
