import {
  SHOW_WARNING,
  HIDE_WARNING,
  WarningState,
  WarningStateActionTypes,
} from './types';

export const initialWarningState: WarningState = {
  isWarningVisible: false,
  warningSetup: {},
};
export const warningReducer = (
  state = initialWarningState,
  action: WarningStateActionTypes
): WarningState => {
  // eslint-disable-line
  switch (action.type) {
    case SHOW_WARNING: {
      return {
        ...state,
        isWarningVisible: true,
        warningSetup: action.payload || state.warningSetup,
      };
    }
    case HIDE_WARNING:
      return {
        ...state,
        isWarningVisible: false,
      };
    default:
      return state;
  }
};
