import {
  PROFILE_VALIDATION_SIZE,
  ProfileValidationSizeState,
  ProfileValidationSizeAction,
} from './types';

export const initialProfileValidationSizeState: ProfileValidationSizeState = {
  width: 1336,
  height: 866,
};
export const profileValidationSize = (
  state = initialProfileValidationSizeState,
  action: ProfileValidationSizeAction
): ProfileValidationSizeState => {
  if (action.type === PROFILE_VALIDATION_SIZE) {
    return action.payload;
  }
  return state;
};
