import { SETTINGS_TYPES } from './enums';

export const FETCH_SYSTEM_SETTINGS_REQUEST = 'FETCH_SYSTEM_SETTINGS_REQUEST';
export const FETCH_SYSTEM_SETTINGS_SUCCESS = 'FETCH_SYSTEM_SETTINGS_SUCCESS';
export const FETCH_PROFILE_SETTINGS_REQUEST = 'FETCH_PROFILE_SETTINGS_REQUEST';
export const FETCH_PROFILE_SETTINGS_SUCCESS = 'FETCH_PROFILE_SETTINGS_SUCCESS';
export const TOGGLE_SETTINGS_PAGE = 'TOGGLE_SETTINGS_PAGE';

export interface FetchSystemSettingsMessage {
  callback?: ((isAuthenticated: boolean) => void) | null;
  password: string;
  isGetMetadata: boolean;
}

export interface FetchProfileSettingsMessage {
  callback?: ((isAuthenticated: boolean) => void) | null;
  password: string;
  isGetMetadata: boolean;
  profileId: string;
}

//TODO (ROIP) - be more specific about the settings!
export interface Settings {
  isAuthenticated: boolean;
  metadata: {
    type?: string;
    fields?: {
      [key: string]: {
        type: string;
        description?: string;
        options?: {
          enumKey: string;
          enumValue: string;
          enumDesc?: string;
        };
        isWriteProtected?: boolean;
        minValue?: number;
        maxValue?: number;
        minStep?: number;
      };
    };
  };
  values: {
    [key: string]:
      | boolean
      | string
      | number
      | {
          [key: string]: string;
        };
  };
}

export interface SettingsState {
  systemSettings: Settings;
  profileSettings: Settings;
  isSettingsPageVisible: boolean;
  settingsPageType: SETTINGS_TYPES;
  settingsProfileCombinationId: string | null;
}

export interface ToggleSettingsPageMessage {
  settingsPageType: SETTINGS_TYPES;
  settingsProfileCombinationId: string | null;
}

export interface FetchSystemSettingsRequestAction {
  type: typeof FETCH_SYSTEM_SETTINGS_REQUEST;
  payload: FetchSystemSettingsMessage;
}

export interface FetchSystemSettingsSuccessAction {
  type: typeof FETCH_SYSTEM_SETTINGS_SUCCESS;
  payload: Settings;
}

export interface FetchProfileSettingsRequestAction {
  type: typeof FETCH_PROFILE_SETTINGS_REQUEST;
  payload: FetchProfileSettingsMessage;
}

export interface FetchProfileSettingsSuccessAction {
  type: typeof FETCH_PROFILE_SETTINGS_SUCCESS;
  payload: Settings;
}

export interface ToggleSettingsPageAction {
  type: typeof TOGGLE_SETTINGS_PAGE;
  payload: ToggleSettingsPageMessage;
}

export type SettingsStateActionTypes =
  | FetchSystemSettingsRequestAction
  | FetchSystemSettingsSuccessAction
  | FetchProfileSettingsRequestAction
  | FetchProfileSettingsSuccessAction
  | ToggleSettingsPageAction;
