import CircularProgress from '@material-ui/core/CircularProgress';
import Modal from '@material-ui/core/Modal';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components/macro';
import { ColorPalette } from '../../utils/StyleGuide';

const StyledDiv = styled.div`
  top: 50%;
  height: 28.89%;
  position: absolute;
  transform: translate(-50%, -50%);
  width: 26.25%;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14),
    0px 1px 14px 0px rgba(0, 0, 0, 0.12);
  background-color: ${ColorPalette.white};
  left: ${props => props.leftPercentage};
  display: grid;
  grid-template-rows: 196px auto;
  grid-template-areas: ${props =>
    props.onlyCircularProgress
      ? `
      'circular-progress'
      'text';
      `
      : `
      'circular-progress'
      'circular-progress';
      `};
`;

const StyledCircularProgress = styled(CircularProgress)`
  grid-area: circular-progress;
  color: ${ColorPalette.darkSkyBlue};
  place-self: center;
`;

const StyledTypography = styled(Typography)`
  grid-area: text;
  justify-self: center;
  color: ${ColorPalette.black};
  width: 272px;
  font-size: 20px;
  text-align: center;
`;

const LoadingModal = ({
  message,
  timedMessage,
  open,
  size,
  thickness,
  leftPercentage,
}) => {
  const [timeElasped, setTimeElasped] = useState(false);
  const timeoutRef = useRef();

  useEffect(() => {
    const close = () => {
      clearTimeout(timeoutRef.current);
      setTimeElasped(false);
    };
    if (open) {
      timeoutRef.current = setTimeout(() => setTimeElasped(true), 10000);
    } else {
      close();
    }
    return close;
  }, [open]);

  const getMessage = () => {
    if (!timeElasped && message) {
      return <StyledTypography>{message}</StyledTypography>;
    }
    if (timeElasped && (timedMessage || message)) {
      return <StyledTypography>{timedMessage || message}</StyledTypography>;
    }
    return null;
  };

  return (
    <Modal open={open}>
      <StyledDiv
        leftPercentage={leftPercentage}
        onlyCircularProgress={
          (message && !timeElasped) || (timeElasped && (timedMessage || message))
        }
      >
        <StyledCircularProgress size={size} thickness={thickness} />
        {getMessage()}
      </StyledDiv>
    </Modal>
  );
};

LoadingModal.propTypes = {
  message: PropTypes.string,
  timedMessage: PropTypes.string,
  open: PropTypes.bool,
  size: PropTypes.number,
  thickness: PropTypes.number,
  leftPercentage: PropTypes.string,
};

LoadingModal.defaultProps = {
  message: null,
  timedMessage: null,
  open: false,
  size: 104,
  thickness: 6,
  leftPercentage: '50%',
};

// We need an intermediary variable for handling the recursive nesting.
export default LoadingModal;
