import _, { set, transform } from 'lodash';
import { isArray, isObjectLike, isPlainObject, map, snakeCase } from 'lodash/fp';
import moment from 'moment';

/**
 * calculates modulo - javascript modulo bug prevents us from using it with negative numbers
 * @param n number
 * @param m modulo
 * @return remainder
 */
export const mod = (n, m) => ((n % m) + m) % m;

export const parseByType = (type, value) => {
  switch (type) {
    case 'bool':
      return Boolean(Number(value));
    default:
      return value;
  }
};

export const toSnakeCaseRecursive = node => {
  if (isArray(node)) {
    return map(toSnakeCaseRecursive, node);
  }
  if (isPlainObject(node)) {
    return transform(
      node,
      (result, value, key) =>
        set(
          result,
          snakeCase(key),
          isObjectLike(value) ? toSnakeCaseRecursive(value) : value
        ),
      {}
    );
  }
  return node;
};

const snakeToCamel = str => str.replace(/([_]\w)/g, g => g[1].toUpperCase());

export const toCamelCaseRecursive = node => {
  if (isArray(node)) {
    return map(toCamelCaseRecursive, node);
  }
  if (isPlainObject(node)) {
    return transform(
      node,
      (result, value, key) =>
        set(
          result,
          snakeToCamel(key),
          isObjectLike(value) ? toCamelCaseRecursive(value) : value
        ),
      {}
    );
  }
  return node;
};

/**
 * Deep diff between two object, using lodash
 * @param  {Object} object Object compared
 * @param  {Object} base   Object to compare with
 * @return {Object}        Return a new object who represent the diff
 */
export const difference = (object, base) => {
  function changes(object, base) {
    return _.transform(object, function(result, value, key) {
      if (!_.isEqual(value, base[key])) {
        result[key] =
          _.isObject(value) && _.isObject(base[key]) ? changes(value, base[key]) : value;
      }
    });
  }
  return changes(object, base);
};

export const removeKeysDeep = (object, ignoreKeys = []) => {
  function removeKeys(object) {
    return _.transform(object, function(result, value, key) {
      if (ignoreKeys.indexOf(key) === -1) {
        result[key] = _.isObject(value) ? removeKeys(value) : value;
      }
    });
  }

  return removeKeys(object);
};

export const shiftDateTime = (date, timezone) => moment(date).tz(timezone);

export const longDateTimeFormat = date => date.format('DD/MM/YYYY - HH:mm:ss');

// this creates a grid template from an array of values
// values can be number, string or (for min max)
// an array with 2 items that can be either number or string
export const getGridString = values =>
  values
    .reduce((acc, curr) => {
      if (typeof curr === 'number') {
        return `${acc} ${curr}px `;
      }
      if (typeof curr === 'object') {
        const min = typeof curr[0] === 'number' ? `${curr[0]}px` : curr[0];
        const max = typeof curr[1] === 'number' ? `${curr[1]}px` : curr[1];
        return `${acc} minmax(${min}, ${max}) `;
      }
      return `${acc} ${curr} `;
    }, 0)
    .substring(2);

export const serverDateFormat = 'YYYY_MM_DD-HH_mm_ss.SSSSS';
