import React, { useEffect, useState } from 'react';
import styled from 'styled-components/macro';
import { ColorPalette } from '../../utils/StyleGuide';
import DashHeader from './DashHeader/DashHeader';
import {
  DASH_MENU,
  dashMenuSetup,
  getBreadcrumbText,
  mainMenuSetup,
  subMenuSetup,
} from './dashUtils';
import { MenuSetup } from './types';
import Dashboard from './Dashboard';
import DashMenu from './DashMenu/DashMenu';

const Screen = styled.div`
  background: ${ColorPalette.white};
  width: 100%;
  height: 100%;
`;
const gap = 80;
const contentGap = 84 + gap;

const ContentArea = styled.div`
  position: relative;
  display: flow-root;
  width: calc(100% - ${contentGap}px);
  height: calc(100% - ${contentGap}px);
  background: ${ColorPalette.backgroundGrey};
  top: ${gap/2}px;
  left: ${contentGap - gap/2}px;
  z-index: 0;
`;

const ProfileCenterScreen = () => {
  const [activeMainMenuId, setActiveMainMenuId] = useState<DASH_MENU | null>(null);
  const [activeSubMenuId, setActiveSubMenuId] = useState<DASH_MENU | null>(null);
  const [activeDashMenuId, setActiveDashMenuId] = useState<DASH_MENU | null>(null);
  const [subMenuContent, setSubMenuContent] = useState<MenuSetup[]>([]);
  const [isMainMenu, setIsMainMenu] = useState(true);

  useEffect(() => {
    mainMenuCallback(mainMenuSetup[0].id);
  }, []);

  const mainMenuCallback = (menuId: DASH_MENU, force?: boolean) => {
    if (menuId === activeMainMenuId && !force) {
      return;
    }
    setActiveMainMenuId(menuId);
    if (subMenuSetup[menuId]) {
      const firstChildMenuId = subMenuSetup[menuId][0].id;
      setSubMenuContent(subMenuSetup[menuId]);
      setActiveSubMenuId(firstChildMenuId);
      setIsMainMenu(false);
    } else {
      setActiveSubMenuId(null);
    }
  };

  const subMenuCallback = (menuId: DASH_MENU, force?: boolean) => {
    if (menuId === activeSubMenuId && !force) {
      return;
    }
    setActiveSubMenuId(menuId);
    setActiveDashMenuId(null);
  };

  const onClickLogo = () => {
    mainMenuCallback(mainMenuSetup[0].id);
    setIsMainMenu(true);
    setActiveSubMenuId(null);
    setActiveDashMenuId(null);
  };

  const onClickBreadcrumb = (idx: number) => {
    switch (idx) {
      case 0:
        setIsMainMenu(true);
        break;
      case 1:
        if (activeSubMenuId) {
          setIsMainMenu(false);
        }
        break;
      default:
        break;
    }
  };

  const mainText: string | null =
    activeMainMenuId && getBreadcrumbText(activeMainMenuId, mainMenuSetup);

  const subText: string | null =
    activeMainMenuId &&
    activeSubMenuId &&
    subMenuSetup[activeMainMenuId] &&
    getBreadcrumbText(activeSubMenuId, subMenuSetup[activeMainMenuId]);

  const dashText: string | null =
    activeSubMenuId &&
    activeDashMenuId &&
    dashMenuSetup[activeDashMenuId] &&
    getBreadcrumbText(activeDashMenuId, dashMenuSetup[activeDashMenuId]);

  const breadcrumbTexts: (string | null)[] = [mainText, subText, dashText];

  const activeMenu = activeDashMenuId || activeSubMenuId || activeMainMenuId;

  /*

    Main menu -> Sub menu -> Dash menu
    (we just flip the order in the markup to place layers in the right order)
 */

  return (
    <Screen>
      <DashHeader
        version={{ text: 'Version 3.2', color: ColorPalette.black }}
        breadcrumbTexts={breadcrumbTexts}
        onClickLogo={onClickLogo}
        onClickBreadcrumb={onClickBreadcrumb}
      />
      <ContentArea>
        {!!activeMenu &&
          [activeMenu].map((curr: string) => {
            switch (curr) {
              default:
                return <Dashboard activeMenu={activeMenu} />;
            }
          })}
      </ContentArea>
      <DashMenu
        isMainMenu={isMainMenu}
        mainMenuSetup={{
          content: mainMenuSetup,
          active: activeMainMenuId,
          callback: mainMenuCallback,
        }}
        subMenuSetup={{
          content: subMenuContent,
          active: activeSubMenuId,
          callback: subMenuCallback,
        }}
        toggleCallback={() => setIsMainMenu(x => !x)}
      />
    </Screen>
  );
};

export default ProfileCenterScreen;
