import { withStyles } from '@material-ui/core';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { POPUP_SCREEN, SCREEN } from '../../store/ui/enums';
import { ColorPalette, headerHeight, textStylesCatalog } from '../../utils/StyleGuide';
import OptionsMenu from './OptionsMenu';

const iconLogo = require('../../images/Logo.svg');
const verticalDividerImg = require('../../images/VerticalDivider.svg');

const styles = {
  toolbar: {
    padding: 0,
    height: headerHeight,
    backgroundColor: ColorPalette.white,
  },
  logoImg: {
    marginTop: 16,
    marginBottom: 18,
    marginLeft: 25,
    width: 52,
    height: 52,
  },
  verticalLine: {
    height: 46,
    marginLeft: 26,
  },
  demoLabel: {
    width: 118,
    height: 37,
    color: ColorPalette.defectFailed,
    flexGrow: 0.9,
    fontSize: 30,
  },
};

function generateTopBarButton(data) {
  const {
    icon,
    alt,
    onClick,
    customizedButtonImageClass,
    customizedButtonMarginClass,
    disabled,
  } = data;

  return (
    <Button
      key={alt}
      variant="text"
      color="default"
      className={customizedButtonMarginClass}
      onClick={onClick}
      disabled={disabled || false}
      id={`${alt.replace(' ', '-')}-top-bar-button`}
    >
      <img src={icon} className={customizedButtonImageClass} alt={alt} />
    </Button>
  );
}

class TopBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isDemoMode: false };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    return {
      isDemoMode: nextProps.demoMode
        ? nextProps.demoMode.value === '1'
        : prevState.isDemoMode,
    };
  }

  render() {
    const {
      logoImage,
      classes,
      buttons,
      position,
      verticalDivider,
      generateOnTopBarButtonClick,
    } = this.props;
    const { isDemoMode } = this.state;
    const companyName = 'INSPEKTO';
    const productionPlant = 'S70';
    return (
      <AppBar color="inherit" position={position} id="top-bar">
        <Toolbar className={classes.toolbar}>
          <div id="top-bar-logo-container">
            <img
              className={classes.logoImg}
              src={logoImage}
              alt="Inspekto Logo"
              id="top-bar-logo-image"
            />
          </div>
          <div>
            <img
              className={classes.verticalLine}
              src={verticalDivider}
              alt="Vertical Divider"
            />
          </div>
          <Typography className={classes.topBarTextCompany} id="top-bar-company-name">
            {companyName}
          </Typography>
          <Typography
            className={classes.topBarTextPlant}
            style={{ flexGrow: isDemoMode ? 0.1 : 1 }}
            id="top-bar-plant-name"
          >
            {productionPlant}
          </Typography>
          {isDemoMode ? (
            <Typography className={classes.demoLabel} id="top-bar-demo-label">
              {'Demo mode'}
            </Typography>
          ) : null}
          <Typography id="top-bar-buttons">
            {buttons.map(button => generateTopBarButton(button))}
          </Typography>
          <OptionsMenu
            options={[
              {
                title: 'Profiles',
                onClick: generateOnTopBarButtonClick(SCREEN.PROFILE_MANAGEMENT),
              },
              {
                title: 'S70 settings',
                onClick: generateOnTopBarButtonClick(POPUP_SCREEN.SETTINGS),
              },
              {
                title: 'About INSPEKTO',
                onClick: generateOnTopBarButtonClick(POPUP_SCREEN.ABOUT),
              },
            ]}
          />
        </Toolbar>
      </AppBar>
    );
  }
}

TopBar.propTypes = {
  // from theme
  classes: PropTypes.shape({}).isRequired,

  logoImage: PropTypes.string,
  verticalDivider: PropTypes.string,
  buttons: PropTypes.arrayOf(
    PropTypes.shape({
      onClick: PropTypes.func.isRequired,
      icon: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
      alt: PropTypes.string.isRequired,
    })
  ),
  position: PropTypes.string,
  generateOnTopBarButtonClick: PropTypes.func.isRequired,
};

TopBar.defaultProps = {
  logoImage: iconLogo,
  verticalDivider: verticalDividerImg,
  buttons: [],
  position: 'absolute',
};

const mapStateToProps = ({ settings }) => ({
  demoMode: settings.systemSettings.values['isDebugMode'],
});

export default withStyles({ ...styles, ...textStylesCatalog })(
  connect(mapStateToProps)(TopBar)
);
