import { DASH_MENU } from './dashUtils';

export enum ANIMATION_DIRECTION {
  TOP = 'TOP',
  RIGHT = 'RIGHT',
  BOTTOM = 'BOTTOM',
  LEFT = 'LEFT',
}

export interface MenuSetup {
  id: DASH_MENU;
  text: string;
  icon: string;
}

export interface MenuConfig {
  content: MenuSetup[];
  active: DASH_MENU | null;
  callback: (id: DASH_MENU) => void;
}

export type SubMenuObject = {
  [key in keyof typeof DASH_MENU]: MenuSetup[];
};
