import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import log from 'loglevel';
import {
  fetchFixiStateRequestAction,
  fetchSystemSettingsRequestAction,
  fetchSystemTimeRequestAction,
  fetchMiniProfileCombosRequestAction,
  fetchMiniSequencesRequestAction,
} from '../../store/actions';
import api from '../../store/api';
import { SYS_ERRORS } from '../../store/errors/enums';
import IntroScreen from '../IntroScreen/IntroScreen';
import ErrorDialog from '../Common/ErrorDialog';
import ProfileCenterScreen from '../ProfileCenter/ProfileCenterScreen';

/**
 * Higher order component.
 * Shows inspekto loading screen or WrappedComponent
 * 1. show loading screen until initial requests to server are answered
 * - health check
 * - system time
 * - system settings
 * - mini profile combos
 * - mini sequence
 * - first fixi state
 * 2. make sure backend is connected with periodic health checks
 * if it is not connected it we show loading screen instead of WrappedComponent
 * 3. has a mechanism to reload WrappedComponent once backend is up again
 */
const waitForServer = WrappedComponent => {
  class WaitForServer extends React.Component {
    constructor(props) {
      super(props);

      this.blockers = {
        systemTimeFetched: props.fetchSystemTimeRequestAction,
        systemSettingsFetched: props.fetchSystemSettingsRequestAction,
        profileCombosFetched: props.fetchMiniProfileCombosRequestAction,
        activeProfileFetched: props.fetchFixiStateRequestAction,
        sequencesFetched: props.fetchMiniSequencesRequestAction,
      };

      this.state = {
        isBackendDown: true,
        waitForServerDone: false,
        // initiate all blocking api states with `false`
        ...Object.keys(this.blockers).reduce(
          (obj, key) => ({ ...obj, [key]: false }),
          {}
        ),
      };
    }

    clockId = 0;
    healthInterval = 0;
    errorMessageInterval = 0;

    componentDidMount() {
      this.healthCheck();
      this.tick();
    }

    componentWillUnmount() {
      clearTimeout(this.clockId);
      clearTimeout(this.healthInterval);
    }

    // will reload page in case backend crashes and comes back alive
    healthCheck = async () => {
      const { isBackendDown, waitForServerDone } = this.state;
      const { error } = this.props;

      try {
        await api.healthCheck();
        if (waitForServerDone && isBackendDown) {
          log.info(`healthCheck is good after crash. reloading`);
          window.location.reload();
          return;
        }
        clearTimeout(this.healthInterval);
        this.healthInterval = setTimeout(this.healthCheck, 10000);
      } catch (e) {
        if (!isBackendDown) {
          log.info(`healthCheck error - backend is down ${e.message}.`);
          if (
            error.id === SYS_ERRORS.CAMERA_DISCONNECTED ||
            error.id === SYS_ERRORS.PROCESS_STOPPED ||
            error.id === SYS_ERRORS.UNKNOWN_ERROR
          ) {
            log.info('Delayed restart in wait for server');

            // Wait before setting backend as down and showing again the intro screen,
            // For the error message to be displayed first in the context of when the error
            // first occurred
            clearTimeout(this.errorMessageInterval);
            this.errorMessageInterval = setTimeout(() => {
              this.setState({ isBackendDown: true });
              clearTimeout(this.healthInterval);
              this.healthInterval = setTimeout(this.healthCheck, 1000);
            }, 5000);
            return;
          } else {
            this.setState({ isBackendDown: true });
          }
        }

        clearTimeout(this.healthInterval);
        this.healthInterval = setTimeout(this.healthCheck, 10000);
      }
    };

    tick = async () => {
      const entries = Object.entries(this.blockers);

      try {
        await api.healthCheck();

        for (const entry of entries) {
          const [key, func] = entry;
          func({
            callback: () => {
              log.info('WaitForServer request', key, 'done');
              this.setState({ [key]: true });
            },
          });
        }

        clearTimeout(this.clockId);
        this.setState({ waitForServerDone: true, isBackendDown: false });
      } catch (e) {
        clearTimeout(this.clockId);
        this.clockId = setTimeout(this.tick, 1000);
        log.info(`waitForServer error ${e.message}`);
      }
    };

    render() {
      const { blockers, state } = this;
      const { error } = this.props;
      const { waitForServerDone, isBackendDown } = state;

      const falsyBlockers = Object.keys(blockers).filter(key => !state[key]).length > 1;
      return !waitForServerDone || falsyBlockers || isBackendDown ? (
        <>
          {error ? <ErrorDialog error={error} /> : null}
          <ProfileCenterScreen />
        </>
      ) : (
        <WrappedComponent {...this.props} />
      );
    }
  }

  WaitForServer.propTypes = {
    fetchSystemSettingsRequestAction: PropTypes.func.isRequired,
    fetchSystemTimeRequestAction: PropTypes.func.isRequired,
    fetchFixiStateRequestAction: PropTypes.func.isRequired,
    fetchMiniProfileCombosRequestAction: PropTypes.func.isRequired,
    fetchMiniSequencesRequestAction: PropTypes.func.isRequired,
  };

  const mapDispatchToProps = {
    fetchFixiStateRequestAction,
    fetchSystemSettingsRequestAction,
    fetchSystemTimeRequestAction,
    fetchMiniProfileCombosRequestAction,
    fetchMiniSequencesRequestAction,
  };

  const mapStateToProps = ({ error }) => ({
    error: error,
  });

  return connect(mapStateToProps, mapDispatchToProps)(WaitForServer);
};

export default waitForServer;
