/* eslint-disable no-nested-ternary */
import React, { useRef, useEffect, useMemo, useState } from 'react';
import styled, { css } from 'styled-components/macro';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import {
  ColorPalette,
  longAnimationOut,
  materialAnimation,
} from '../../utils/StyleGuide';

const animate = `box-shadow ${longAnimationOut}, border ${materialAnimation}, background ${materialAnimation}, opacity ${materialAnimation}, color ${materialAnimation}`;

const disabledColor = ColorPalette.passiveLightGrey;
const borderWidth = 2;

const gradientOutlineCss = css`
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: ${-borderWidth}px;
  border-radius: inherit;
  background: ${props => props.gradientBorder};
`;

const activeCss = css`
  box-shadow: ${props => props.boxshadow};
`;

const AnimButton = styled(({ ...props }) => {
  const { pushAnimation, gradientText, gradientBorder, hoverShadow, ...rest } = props;
  return (
    <Button
      classes={{ label: 'label' }}
      {...rest}
      onFocus={e => e.target.blur()} // this is the important line
    />
  );
})`
  width: ${props => props.width};
  height: ${props => props.height};
  min-width: 0;
  border-radius: ${props => props.radius};
  background: ${props =>
    props.background
      ? (props.blocked || props.isdisabled) && props.disabletype === 'color'
        ? disabledColor
        : props.background
      : 'none !important'};
  margin: ${props => props.margin};
  padding: 0;
  box-shadow: ${props => (props.blocked || props.isdisabled ? 'none' : props.boxshadow)};
  float: ${props => props.float};
  border: ${borderWidth}px solid
    ${props =>
      props.border
        ? (props.blocked || props.isdisabled) && props.disabletype === 'color'
          ? disabledColor
          : props.border
        : 'none'};
  opacity: ${props =>
    (props.blocked || props.isdisabled) && props.disabletype === 'transparency' ? 0 : 1};
  transition: ${animate};
  cursor: initial;
  &:hover {
    box-shadow: ${props =>
      props.blocked || props.isdisabled
        ? 'none'
        : props.allowhover
        ? props.hoverShadow
        : props.boxshadow};
    background: ${props =>
      props.background
        ? (props.blocked || props.isdisabled) && props.disabletype === 'color'
          ? disabledColor
          : props.background
        : 'none !important'};
    cursor: ${props => (props.blocked || props.isdisabled ? 'initial' : 'pointer')};
  }

  &:active {
    ${props => (props.pushAnimation ? activeCss : '')}
  }

  // disable ugly ripple effect
  .MuiTouchRipple-root {
    display: none;
  }

  .label {
    width: 100%;
    height: 100%;
    display: block;
    text-transform: none;
    padding-left: ${props => (props.gap ? '38px' : '0')};
    background: ${props => (props.gradientText ? '#FFFFFF' : 'none')};
    color: ${props =>
      props.gradientText
        ? '#000000'
        : props.textcolor
        ? (props.blocked || props.isdisabled) && props.disabletype === 'color'
          ? props.disabledfontcolor
          : props.textcolor
        : '#FFFFFF'};
    font-size: ${props => props.fontSize};
    font-weight: ${props => props.weight};
    line-height: ${props => props.height};
    pointer-events: none;
    transition: ${animate};
    mix-blend-mode: ${props => (props.gradientText ? 'screen' : 'normal')};
    border-radius: ${props =>
      props.gradientText
        ? `${parseInt(props.radius.split('px')[0], 10) - borderWidth / 2}px`
        : 'inherit'};
  }

  // gradient border
  &:before {
    ${props => (props.gradientBorder ? gradientOutlineCss : '')}
  }
`;

const IconContainer = styled.div`
  position: absolute;
  width: 28px;
  height: 28px;
  top: 4px;
  left: 14px;
`;

const TextButton = props => {
  // eslint-disable-next-line no-unused-vars
  const {
    children,
    styleOverride,
    name,
    text,
    blocked,
    disabled,
    disabledFontColor,
    buttonCallback,
    tooltip,
    tooltipCallback,
    tooltipSide,
    tooltipPosition,
    disableType,
    color,
    boxShadow,
    hoverShadow,
    width,
    height,
    margin,
    background,
    float,
    border,
    gradientBorder,
    gradientText,
    pushAnimation,
    radius,
    weight,
    fontSize,
    allowHover,
  } = props;
  const [hover, setHover] = useState(false);
  const [onClickName, setOnClickName] = useState('');
  const buttonCallbackRef = useRef(buttonCallback);
  const childrenRef = useRef(children);

  // effect populate button callback ref in order to not rerender animButton
  useEffect(() => {
    buttonCallbackRef.current = buttonCallback;
  }, [buttonCallback]);

  // effect to call button callback on onClickName change
  useEffect(() => {
    if (onClickName) {
      callButtonCallback(onClickName);
      setOnClickName(() => '');
    }
  }, [onClickName]);

  useEffect(() => {
    childrenRef.current = children;
  }, [children]);

  const callButtonCallback = name => {
    buttonCallbackRef.current(name);
  };

  return useMemo(() => {
    let elementId = `${name}-button`;
    if (name === 'profile-validation' && text === 'Back') {
      elementId = 'back-button';
    }
    // we assume children is an image or animated icon with 100% width and height
    const child = !!childrenRef.current
      ? React.cloneElement(childrenRef.current, { hover: hover })
      : null;
    return (
      <AnimButton
        style={styleOverride}
        id={elementId}
        width={width}
        height={height}
        blocked={blocked ? 'ok' : null}
        isdisabled={disabled ? 'ok' : null}
        disabledfontcolor={disabledFontColor}
        disabletype={disableType}
        background={background}
        textcolor={color}
        boxshadow={boxShadow}
        hoverShadow={hoverShadow}
        float={float}
        border={border}
        gradientBorder={gradientBorder}
        gradientText={gradientText}
        pushAnimation={pushAnimation}
        radius={radius}
        weight={weight}
        fontSize={fontSize}
        onClick={blocked || disabled ? null : name => setOnClickName(() => name)}
        margin={margin}
        gap={child ? 1 : 0}
        allowhover={allowHover ? 1 : 0}
        onMouseOver={e => {
          if (!(disabled || blocked)) {
            setHover(true);
          }
          if (tooltip) {
            tooltipCallback('open', e, tooltip, tooltipSide, tooltipPosition);
          }
        }}
        onMouseOut={() => {
          if (!(disabled || blocked)) {
            setHover(false);
          }
          if (tooltip) {
            tooltipCallback('close');
          }
        }}
      >
        {text}
        {child ? <IconContainer>{child}</IconContainer> : null}
      </AnimButton>
    );
  }, [
    hover,
    styleOverride,
    name,
    text,
    blocked,
    disabled,
    tooltip,
    tooltipCallback,
    tooltipSide,
    tooltipPosition,
    disableType,
    color,
    boxShadow,
    disabledFontColor,
    width,
    height,
    margin,
    background,
    float,
    border,
    radius,
    weight,
    fontSize,
    allowHover,
    gradientBorder,
    gradientText,
    hoverShadow,
    pushAnimation,
  ]);
};

TextButton.propTypes = {
  children: PropTypes.shape({}),
  text: PropTypes.string.isRequired,
  name: PropTypes.string,
  styleOverride: PropTypes.shape({}),
  buttonCallback: PropTypes.func,
  disabled: PropTypes.bool,
  disableType: PropTypes.string,
  blocked: PropTypes.bool,
  disabledFontColor: PropTypes.string,
  tooltip: PropTypes.string,
  tooltipCallback: PropTypes.func,
  tooltipSide: PropTypes.string,
  tooltipPosition: PropTypes.shape({
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired,
  }),
  width: PropTypes.string,
  height: PropTypes.string,
  margin: PropTypes.string,
  boxShadow: PropTypes.string,
  hoverShadow: PropTypes.string,
  background: PropTypes.string,
  color: PropTypes.string,
  float: PropTypes.string,
  border: PropTypes.string,
  gradientBorder: PropTypes.string,
  gradientText: PropTypes.bool,
  pushAnimation: PropTypes.bool,
  radius: PropTypes.string,
  weight: PropTypes.number,
  fontSize: PropTypes.string,
  allowHover: PropTypes.bool,
};

TextButton.defaultProps = {
  children: null,
  name: '',
  styleOverride: null,
  disabled: false,
  disableType: 'color', // transparency
  buttonCallback: () => null,
  blocked: false,
  disabledFontColor: disabledColor,
  tooltip: null,
  tooltipCallback: null,
  tooltipSide: null,
  tooltipPosition: null,
  boxShadow: '0 2px 3px 0 rgba(0, 0, 0, 0.2)',
  hoverShadow: '0 4px 12px 0 rgba(0, 0, 0, 0.28)',
  color: null,
  float: 'none',
  width: '176px',
  height: '40px',
  margin: '0 10px 0 10px',
  border: null,
  // !! IMPORTANTE !!
  // To make gradient text work you need to explicitly set z-Index to 7 in the styleOverride.
  // Not ideal, temporary measure before the complete refactor
  gradientBorder: null,
  gradientText: false,
  pushAnimation: false,
  radius: '20px',
  weight: 600,
  fontSize: '16px',
  allowHover: true,
};
export default TextButton;
