import { SCREEN, POPUP_SCREEN, PEND_UPDATE_REASON } from './enums';

export const UPDATE_SCREEN = 'UPDATE_SCREEN';
export const UPDATE_POPUP_SCREEN = 'UPDATE_POPUP_SCREEN';
export const PEND_UPDATE = 'PEND_UPDATE';
export const FINISHED_UPDATE = 'FINISHED_UPDATE';
export const PEND_LOADING_PROFILE_COMBO = 'PEND_LOADING_PROFILE_COMBO';
export const UPDATE_FRAME_IMAGE_TIMER = 'UPDATE_FRAME_IMAGE_TIMER';
export const SET_FRAME_IMAGE_TIMER = 'SET_FRAME_IMAGE_TIMER';
export const STATE_WAS_UNLOADED = 'STATE_WAS_UNLOADED';

export interface PendLoadingProfileMessage {
  isLoading: boolean;
  profileComboId: string;
  sequenceId: string;
}

export interface UiState {
  screen: SCREEN;
  popupScreen: POPUP_SCREEN;
  pendingUpdate: PEND_UPDATE_REASON;
  frameImageTimer: number;
  wasUnloaded: boolean;
}

export interface UpdateScreen {
  type: typeof UPDATE_SCREEN;
  payload: SCREEN;
}

export interface UpdatePopupScreen {
  type: typeof UPDATE_POPUP_SCREEN;
  payload: POPUP_SCREEN;
}

export interface PendUpdate {
  type: typeof PEND_UPDATE;
  payload: PEND_UPDATE_REASON;
}

export interface FinishedUpdate {
  type: typeof FINISHED_UPDATE;
}

export interface StateWasUnloaded {
  type: typeof STATE_WAS_UNLOADED;
  payload: boolean;
}

export interface PendLoadingProfileCombo {
  type: typeof PEND_LOADING_PROFILE_COMBO;
  payload: PendLoadingProfileMessage;
}

export interface UpdateFrameImageTimer {
  type: typeof UPDATE_FRAME_IMAGE_TIMER;
  payload: number;
}

export interface SetFrameImageTimer {
  type: typeof SET_FRAME_IMAGE_TIMER;
  payload: boolean;
}

export type UiActionTypes =
  | UpdateScreen
  | UpdatePopupScreen
  | PendUpdate
  | FinishedUpdate
  | StateWasUnloaded
  | PendLoadingProfileCombo
  | UpdateFrameImageTimer
  | SetFrameImageTimer;
