import log from 'loglevel';
import {
  UiActionTypes,
  UiState,
  UPDATE_SCREEN,
  UPDATE_POPUP_SCREEN,
  PEND_UPDATE,
  FINISHED_UPDATE,
  UPDATE_FRAME_IMAGE_TIMER,
  STATE_WAS_UNLOADED,
} from './types';
import { SCREEN, POPUP_SCREEN, PEND_UPDATE_REASON } from './enums';

export const initialUiState: UiState = {
  screen: SCREEN.MAIN,
  popupScreen: POPUP_SCREEN.NO_POPUP,
  pendingUpdate: PEND_UPDATE_REASON.NOT_PENDING,
  frameImageTimer: 2000,
  wasUnloaded: false,
};
export const uiReducer = (state = initialUiState, action: UiActionTypes): UiState => {
  // eslint-disable-line
  switch (action.type) {
    case UPDATE_SCREEN: {
      log.info('screen update from', state.screen, 'to', action.payload);
      return {
        ...state,
        screen: action.payload,
      };
    }
    case UPDATE_POPUP_SCREEN:
      log.info('popup screen update from', state.popupScreen, 'to', action.payload);
      return {
        ...state,
        popupScreen: action.payload,
      };
    // explicitly wait for algo for a profile combo update
    case PEND_UPDATE:
      log.info(`pend update ${action.payload}`);
      return {
        ...state,
        pendingUpdate: action.payload,
      };
    // algo updated profile combo
    case FINISHED_UPDATE:
      return {
        ...state,
        pendingUpdate: PEND_UPDATE_REASON.NOT_PENDING,
      };
    case UPDATE_FRAME_IMAGE_TIMER:
      return {
        ...state,
        frameImageTimer: action.payload,
      };
    // update the state was unloaded
    case STATE_WAS_UNLOADED:
      return {
        ...state,
        wasUnloaded: action.payload,
      };
    default:
      return state;
  }
};
