import log from 'loglevel';
import { ApiHost } from './hosts';
import io from '../utils/cSockette';
import framesStore from './framesStore';
import { AnnotatedFrame } from './types';
export const imagePrefix = 'data:image/*;base64,';

const handleOnNewAnnotatedFrame = (annotatedFrame: AnnotatedFrame) => {
  const { frame } = annotatedFrame;
  if (!frame) {
    log.error('Missing frame data.');
  }
  annotatedFrame.frame = `${imagePrefix}${frame}`;

  framesStore.setState(annotatedFrame);
};

// TODO(roip) to add support for multiple cameras
// TODO make into a singleton
// TODO accept camera instead of array
// TODO close socket when there are no listeners
// TODO frameStore should handle frameSocket
export default (cameraId: string) => {
  const socket = io(`${ApiHost}?camera=${cameraId}`);
  socket.on('connect', () => {
    log.info(`frameSocket ${cameraId} socket connected`);
  });
  socket.on('connect_error', (data: any) => {
    log.info(`${cameraId} frameSocket connection error - ${data}`);
  });
  socket.on('disconnect', () => {
    framesStore.removeStickyState();
    log.info(`${cameraId} frameSocket disconnected`);
  });
  socket.on(`new_frame_${cameraId}`, handleOnNewAnnotatedFrame);
  return socket;
};
