import React, { useState, useEffect } from 'react';
import useInterval from '../Hooks/useInterval';
import log from 'loglevel';

import api from '../../store/api';

import LogoutDialog from '../LogoutDialog';
import OTPDialog from '../OTPDialog';
import CompleteDialog from '../SetupScreen/CompleteDialog';

// lock status types
export const LOCK_STATUS = {
  LOCKED: 1,
  UNLOCKED: 2,
  ERROR: 3,
};

let electron = null;
let remote = null;
let isDev = false;
let disableHardening = process.env.REACT_APP_DISABLE_HARDENING === '1';
try {
  electron = window.require('electron');
  isDev = window.require('electron-is-dev');
  remote = electron.remote;

  /*
  Using electron's IPC system, notify the main process that the client is not in kiosk mode.
  This happens on startup if the client is either in development mode or was built with the REACT_APP_DISABLE_HARDENING flag.
  */
  const ipcRenderer = electron.ipcRenderer;
  if (isDev || disableHardening) {
    ipcRenderer.send('locking-channel', false);
  }
} catch {
  log.warn('You are not using Electron. Some features will not work.');
}

const FETCH_STATUS_INTERVAL = 10000;

const SuccessDialog = ({ open, label }) => <CompleteDialog open={open} label={label} />;

const defaultDialogState = {
  failedAttempts: 0,
  showDialog: false,
  showCompleteDialog: false,
  showError: false,
};

const initialDialogState = {
  dialogTimeout: null,
  ...defaultDialogState,
};

function LockerDialog(props) {
  const [isLocked, setIsLocked] = useState(true);
  const [logoutDialogState, setLogoutDialogState] = useState(initialDialogState);
  const [OTPDialogState, setOTPDialogState] = useState(initialDialogState);

  const fetchStatus = async () => {
    const response = await api.getLockStatus();
    if (response.status) {
      const APIIsLocked = response.status === LOCK_STATUS.LOCKED;
      if (isLocked !== APIIsLocked) {
        if (APIIsLocked) {
          setIsLocked(true);
        } else if (!APIIsLocked) {
          setIsLocked(false);
        }
      }
    }
  };

  const openLogoutDialog = () => {
    setLogoutDialogState(logoutDialogState => ({
      ...logoutDialogState,
      showDialog: true,
      showError: false,
    }));
  };

  const closeLogoutDialog = () => {
    setLogoutDialogState(state => ({ ...state, ...defaultDialogState }));
  };

  const openOTPDialog = () => {
    setOTPDialogState(OTPDialogState => ({
      ...OTPDialogState,
      showDialog: true,
      showError: false,
    }));
  };

  const closeOTPDialog = () => {
    setOTPDialogState(state => ({ ...state, ...defaultDialogState }));
  };

  const sendDialog = async otp => {
    try {
      let res;
      try {
        res = await api.toggleLock(otp);
      } catch {
        res = await api.backupUnlock(otp);
      }

      const isLocked =
        res.status === LOCK_STATUS.UNLOCKED
          ? false
          : res.status === LOCK_STATUS.LOCKED
          ? true
          : undefined;

      if (isLocked !== undefined) {
        setIsLocked(isLocked);
        setOTPDialogState(state => ({
          ...state,
          ...defaultDialogState,
          showCompleteDialog: true,
        }));

        clearTimeout(OTPDialogState.dialogTimeout);
        const newTimeout = setTimeout(
          () => setOTPDialogState(state => ({ ...state, showCompleteDialog: false })),
          2000
        );
        setOTPDialogState(state => ({ ...state, dialogTimeout: newTimeout }));
        return;
      }
    } catch (error) {
      log.warn(error);
    }
    // On the event nothing was successful
    setOTPDialogState({
      ...OTPDialogState,
      showError: true,
      failedAttempts: OTPDialogState.failedAttempts + 1,
    });
  };

  const handleKeydown = event => {
    // Since this is a callback function, to avoid getting a "stale" state
    // we will get the latest one using function updates and returning the same state.
    const F12_KEYCODE = 123;
    if (event.keyCode === F12_KEYCODE) {
      let lastDialogState;
      setLogoutDialogState(state => {
        lastDialogState = state;
        return state;
      });

      if (lastDialogState && lastDialogState.showDialog) {
        closeLogoutDialog();
      } else {
        openLogoutDialog();
      }
    }
    const F3_KEYCODE = 114;
    if (event.keyCode === F3_KEYCODE) {
      let lastDialogState;
      setOTPDialogState(state => {
        lastDialogState = state;
        return state;
      });

      if (lastDialogState && lastDialogState.showDialog) {
        closeOTPDialog();
      } else {
        openOTPDialog();
      }
    }
  };

  useInterval(fetchStatus, FETCH_STATUS_INTERVAL);

  useEffect(() => {
    if (isLocked) {
      lockKiosk();
    } else {
      unlockKiosk();
    }
  }, [isLocked]);

  useEffect(() => {
    fetchStatus();
    document.addEventListener('keydown', handleKeydown);
    // Cleanup
    return () => {
      document.removeEventListener('keydown', handleKeydown);
      clearTimeout(logoutDialogState.dialogTimeout);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const { showDialog: showLogoutDialog, showError: showLogoutError } = logoutDialogState;

  const {
    showDialog: showOTPDialog,
    showError: showOTPError,
    showCompleteDialog: showOTPCompleteDialog,
    failedAttempts,
  } = OTPDialogState;

  return (
    <>
      <LogoutDialog
        open={showLogoutDialog}
        hasErrored={showLogoutError}
        noPassword={!isLocked}
        handleClose={closeLogoutDialog}
      />

      <OTPDialog
        open={showOTPDialog}
        hasErrored={showOTPError}
        noPassword={!isLocked}
        showTime={failedAttempts >= 3}
        handleClose={closeOTPDialog}
        handleSend={sendDialog}
      />

      <SuccessDialog
        open={showOTPCompleteDialog}
        label={isLocked ? 'Locked' : 'Unlocked'}
      />
      {props.children}
    </>
  );
}

const unlockKiosk = () => {
  if (remote && !isDev && !disableHardening) {
    const window = remote.getCurrentWindow();
    window.removeListener('blur', focusBack);
    window.setAlwaysOnTop(false);
    window.setVisibleOnAllWorkspaces(false);
    window.setFullScreen(false);
  }
};

const lockKiosk = () => {
  if (remote && !isDev && !disableHardening) {
    const window = remote.getCurrentWindow();
    window.removeListener('blur', focusBack);
    window.on('blur', focusBack);
    window.setAlwaysOnTop(true);
    window.setVisibleOnAllWorkspaces(true);
    window.setFullScreen(true);
  }
};

const focusBack = () => {
  const window = remote.getCurrentWindow();
  window.focus();
};

export default LockerDialog;
