import PropTypes from 'prop-types';
import React from 'react';
import styled, { keyframes } from 'styled-components/macro';

const logoTop = require('../../images/logo_top.png');
const logoBottom = require('../../images/logo_bottom.png');
const inspektoText = require('../../images/logo_inspekto.png');
const amvText = require('../../images/logo_amv.png');
const amvHeight = 8;

const animTime = 0.8;
const bezierOut = 'cubic-bezier(0.68, 0.01, 0.69, 0.99)';
const bezierIn = 'cubic-bezier(0.27, 0.04, 0.33, 1)';

const fadeIn = () => keyframes`
    0%   {opacity: 0; }
    100% {opacity: 1; }
`;
const fromLeft = () => keyframes`
    0%   {transform: translateX(-100%);}
    100% {transform: translateX(0);}
`;
const fromRight = () => keyframes`
    0%   {transform: translateX(100%); }
    100% {transform: translateX(0); }
`;

const CenteredContainer = styled.div`
  position: absolute;
  top: -160px;
`;

const LogoContainer = styled.div`
  position: absolute;
  left: -${props => props.width / 2}px;
  width: ${props => props.width}px;
  height: ${props => props.height}px;
`;

const LogoPartTop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: ${() => fromLeft()} ${animTime}s ${bezierOut},
    ${() => fadeIn()} ${animTime}s ${bezierOut};
`;

const LogoPartBottom = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: ${() => fromRight()} ${animTime}s ${bezierIn} ${animTime - 0.05}s;
`;

const LogoImageTop = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: ${() => fromRight()} ${animTime}s ${bezierOut};
`;

const LogoImageBottom = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: ${() => fromLeft()} ${animTime}s ${bezierIn} ${animTime - 0.05}s;
  animation-fill-mode: backwards;
`;

const Inspekto = styled.img`
  position: absolute;
  top: 157px;
  left: -${props => props.width / 2}px;
  width: ${props => props.width}px;
  height: auto;
  animation: ${() => fadeIn()} 1.4s cubic-bezier(0.43, 0.01, 0.32, 0.99) 0.8s;
  animation-fill-mode: backwards;
`;

const Amv = styled.img`
  position: absolute;
  top: 192px;
  left: -${props => props.width / 2}px;
  width: ${props => props.width}px;
  height: auto;
  animation: ${() => fadeIn()} 1.6s cubic-bezier(0.63, 0.01, 0.52, 0.99) 1.1s;
  animation-fill-mode: backwards;
`;

const InspektoLogoAnimation = ({ width, logoSize, isLoader }) => {
  return (
    <CenteredContainer>
      <LogoContainer width={logoSize} height={logoSize}>
        <LogoPartTop top={true} loader={isLoader}>
          <LogoImageTop src={logoTop} top={true} loader={isLoader} />
        </LogoPartTop>
        <LogoPartBottom loader={isLoader}>
          <LogoImageBottom src={logoBottom} loader={isLoader} />
        </LogoPartBottom>
      </LogoContainer>
      <Inspekto src={inspektoText} width={width} height={amvHeight} />
      <Amv src={amvText} width={width} />
    </CenteredContainer>
  );
};

InspektoLogoAnimation.propTypes = {
  width: PropTypes.number,
  logoSize: PropTypes.number,
};

InspektoLogoAnimation.defaultProps = {
  width: 198,
  logoSize: 138,
};

export default InspektoLogoAnimation;
