import {
  FETCH_MINI_PROFILE_COMBOS_REQUEST,
  FETCH_MINI_PROFILE_COMBOS_SUCCESS,
  FETCH_MINI_PROFILE_COMBOS_FAILURE,
  REMOVE_MINI_PROFILE_COMBO,
  UPDATE_MINI_PROFILE_COMBO,
  UPDATE_MINI_PROFILE_COMBO_SUCCESS,
  FETCH_MINI_SEQUENCES_REQUEST,
  FETCH_MINI_SEQUENCES_SUCCESS,
  FETCH_MINI_SEQUENCES_FAILURE,
  UPDATE_MINI_SEQUENCE,
  MiniProfileCombosState,
  MiniSequencesState,
  FetchMiniProfileCombosRequestAction,
  FetchMiniProfileCombosSuccessAction,
  FetchMiniProfileCombosFailedAction,
  FetchMiniSequencesRequestAction,
  RemoveProfileComboAction,
  UpdateMiniProfileComboAction,
  UpdateMiniProfileComboSuccessAction,
  FetchMiniSequencesSuccessAction,
  FetchMiniSequencesFailedAction,
  UpdateMiniSequenceAction,
} from './types';

/* =============================================>>>>>
= MINI PROFILE COMBINATIONS =
===============================================>>>>> */
export const fetchMiniProfileCombosRequestAction = ({
  callback,
}: {
  callback: (() => void) | null;
}): FetchMiniProfileCombosRequestAction => ({
  type: FETCH_MINI_PROFILE_COMBOS_REQUEST,
  payload: { callback },
});

export const fetchMiniProfileCombosSuccessAction = (
  data: MiniProfileCombosState
): FetchMiniProfileCombosSuccessAction => ({
  type: FETCH_MINI_PROFILE_COMBOS_SUCCESS,
  payload: data,
});

export const fetchMiniProfileCombosFailedAction = (
  error: Error
): FetchMiniProfileCombosFailedAction => ({
  type: FETCH_MINI_PROFILE_COMBOS_FAILURE,
  payload: error,
});

export const removeProfileComboAction = ({
  id,
}: {
  id: string;
}): RemoveProfileComboAction => ({
  type: REMOVE_MINI_PROFILE_COMBO,
  payload: {
    id,
  },
});

export const updateMiniProfileComboAction = (
  data: MiniProfileCombosState
): UpdateMiniProfileComboAction => ({
  type: UPDATE_MINI_PROFILE_COMBO,
  payload: data,
});

export const updateMiniProfileComboSuccessAction = (
  data: MiniProfileCombosState
): UpdateMiniProfileComboSuccessAction => ({
  type: UPDATE_MINI_PROFILE_COMBO_SUCCESS,
  payload: data,
});

/* =============================================>>>>>
= MINI SEQUENCES =
===============================================>>>>> */
export const fetchMiniSequencesRequestAction = ({
  callback,
}: {
  callback: (() => void) | null;
}): FetchMiniSequencesRequestAction => ({
  type: FETCH_MINI_SEQUENCES_REQUEST,
  payload: { callback },
});

export const fetchMiniSequencesSuccessAction = (
  data: MiniSequencesState
): FetchMiniSequencesSuccessAction => ({
  type: FETCH_MINI_SEQUENCES_SUCCESS,
  payload: data,
});

export const fetchMiniSequencesFailedAction = (
  error: Error
): FetchMiniSequencesFailedAction => ({
  type: FETCH_MINI_SEQUENCES_FAILURE,
  payload: error,
});

export const updateMiniSequenceAction = (
  data: MiniSequencesState
): UpdateMiniSequenceAction => ({
  type: UPDATE_MINI_SEQUENCE,
  payload: data,
});
