import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _ from 'lodash';
import PropTypes from 'prop-types';
import React from 'react';
import { connect as connectRedux } from 'react-redux';
import { PEND_UPDATE_REASON } from '../../store/ui/enums';
import { POPUP_SCREEN } from '../../store/ui/enums';

const menuIcon = require('../../images/Menu.svg');
const disabledMenuIcon = require('../../images/Disabled_Menu.svg');
class OptionsMenu extends React.Component {
  static propTypes = {
    menuWidth: PropTypes.number,
    menuItemHeight: PropTypes.number,
    maxNumOfShownItems: PropTypes.number,
    icon: PropTypes.element,
    options: PropTypes.arrayOf(
      PropTypes.shape({
        title: PropTypes.string.isRequired,
        // localized description, i.e. 'Settings', 'Einstellungen'
        onClick: PropTypes.func.isRequired,
        // onClick handler to be called upon user interaction with this menu-item
      })
    ).isRequired,
  };

  state = {
    anchorEl: null,
  };

  handleClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { anchorEl } = this.state;
    const {
      menuWidth,
      menuItemHeight,
      maxNumOfShownItems,
      icon,
      disabledIcon,
      options,
      popupScreen,
    } = this.props;
    if (_.isEmpty(options)) {
      return null;
    }
    return (
      <div id="options-menu">
        <IconButton
          onClick={this.handleClick}
          disabled={
            this.props.pendingUpdate === PEND_UPDATE_REASON.CAPTURING_FRAME ||
            this.props.popupScreen === POPUP_SCREEN.SETTINGS
          }
          id="options-menu-icon"
        >
          {this.props.pendingUpdate === PEND_UPDATE_REASON.CAPTURING_FRAME ||
          this.props.popupScreen === POPUP_SCREEN.SETTINGS
            ? disabledIcon
            : icon}
        </IconButton>
        <Menu
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'center',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          open={Boolean(anchorEl)}
          onClose={this.handleClose}
          PaperProps={{
            style: {
              maxHeight: menuItemHeight * maxNumOfShownItems,
              width: menuWidth,
            },
          }}
          id="options-menu-container"
          getContentAnchorEl={null}
        >
          {options.map(option => (
            <MenuItem
              disabled={option.title !== 'About INSPEKTO' && popupScreen === 'settings'}
              key={option.title}
              onClick={e => {
                this.handleClose();
                option.onClick(e);
              }}
              id={`${option.title.replace(/\s/g, '-')}-options-menu-item`}
            >
              {option.title}
            </MenuItem>
          ))}
        </Menu>
      </div>
    );
  }
}

OptionsMenu.defaultProps = {
  menuWidth: 200,
  menuItemHeight: 48,
  maxNumOfShownItems: 4.5,
  icon: <img src={menuIcon} style={{ width: '8px', height: '38px' }} alt="" />,
  disabledIcon: (
    <img src={disabledMenuIcon} style={{ width: '8px', height: '38px' }} alt="" />
  ),
};

const mapStateToProps = ({ ui }) => {
  return {
    pendingUpdate: ui.pendingUpdate,
    popupScreen: ui.popupScreen,
  };
};

export default connectRedux(mapStateToProps)(OptionsMenu);
