import {
  FETCH_CAMERAS_REQUEST,
  FETCH_CAMERAS_FAILURE,
  FETCH_CAMERAS_SUCCESS,
  CamerasMessage,
  FetchCamerasSuccessAction,
  FetchCamerasRequestAction,
  FetchCamerasFailedAction,
} from './types';

export const fetchCamerasRequestAction = ({
  callback = null,
}: {
  callback: ((data?: any) => void) | null;
}): FetchCamerasRequestAction => ({
  type: FETCH_CAMERAS_REQUEST,
  payload: { callback },
});

export const fetchCamerasSuccessAction = (
  cameras: CamerasMessage
): FetchCamerasSuccessAction => ({
  type: FETCH_CAMERAS_SUCCESS,
  payload: cameras,
});

export const fetchCamerasFailedAction = (error: Error): FetchCamerasFailedAction => ({
  type: FETCH_CAMERAS_FAILURE,
  payload: error,
});
