import log from 'loglevel';
import _ from 'lodash';
import {
  FETCH_SYSTEM_SETTINGS_SUCCESS,
  FETCH_PROFILE_SETTINGS_SUCCESS,
  TOGGLE_SETTINGS_PAGE,
  Settings,
  SettingsState,
  SettingsStateActionTypes,
} from './types';
import { SETTINGS_TYPES } from './enums';

const initialSetting: Settings = {
  isAuthenticated: false,
  values: {},
  metadata: {},
};
export const initialSettingsState: SettingsState = {
  systemSettings: initialSetting,
  profileSettings: initialSetting,
  isSettingsPageVisible: false,
  settingsPageType: SETTINGS_TYPES.SYSTEM,
  settingsProfileCombinationId: null,
};
export const settingsReducer = (
  state = initialSettingsState,
  action: SettingsStateActionTypes
): SettingsState => {
  // eslint-disable-line
  switch (action.type) {
    case FETCH_SYSTEM_SETTINGS_SUCCESS: {
      let newSettings = {};
      if (!_.isEmpty(action.payload)) {
        newSettings = action.payload;
      }
      return {
        ...state,
        systemSettings: { ...state.systemSettings, ...newSettings },
      };
    }
    case FETCH_PROFILE_SETTINGS_SUCCESS: {
      const newSettings = !_.isEmpty(action.payload)
        ? action.payload
        : {
            isAuthenticated: false,
            values: {},
            metadata: {},
          };
      log.info(action.payload);
      return {
        ...state,
        profileSettings: { ...newSettings },
      };
    }
    case TOGGLE_SETTINGS_PAGE:
      return {
        ...state,
        settingsPageType: action.payload.settingsPageType || state.settingsPageType,
        settingsProfileCombinationId: action.payload.settingsProfileCombinationId || null,
      };
    default:
      return state;
  }
};
