import moment from 'moment';
import 'moment-timezone';
import {
  FETCH_SYSTEM_TIME_SETTINGS_REQUEST,
  FETCH_SYSTEM_TIME_SETTINGS_SUCCESS,
  SystemTimeActionTypes,
  SystemTimeState,
} from './types';

export const initialSystemTimeState: SystemTimeState = {
  useNtp: false,
  isFetching: false,
  timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
  timeZoneOptions: [],
};
export const systemTimeSettingsReducer = (
  state = initialSystemTimeState,
  action: SystemTimeActionTypes
): SystemTimeState => {
  switch (action.type) {
    case FETCH_SYSTEM_TIME_SETTINGS_REQUEST:
      return {
        ...state,
        isFetching: true,
      };
    case FETCH_SYSTEM_TIME_SETTINGS_SUCCESS:
      (moment as any).tz.setDefault(action.payload.values.timeZone);
      return {
        ...state,
        useNtp: action.payload.values.useNtp,
        isFetching: false,
        timeZone: action.payload.values.timeZone,
        timeZoneOptions: action.payload.metadata.fields.timeZone.options,
      };
    default:
      return {
        ...state,
        isFetching: false,
      };
  }
};
