import React from 'react';
import log from 'loglevel';
import IntroScreen from './IntroScreen/IntroScreen';

let ipcRenderer = null;
try {
  ipcRenderer = window.require('electron').ipcRenderer;
} catch {
  log.warn('Not using chrome, some electron features will be unavailable');
}

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null, errorInfo: null, showSupportError: false };
  }

  componentDidCatch(error, errorInfo) {
    // global.critical = process.env.NODE_ENV === 'production';
    log.info('CLIENT IS RESTARTING');
    log.info(
      `error caught in ErrorBoundary: ${error.toString()}\n` +
        `stack: ${errorInfo.componentStack}`
    );
    if (process.env.NODE_ENV !== 'development') {
      const showSupportError = ipcRenderer
        ? ipcRenderer.sendSync('oops-channel', 'oops')
        : null;
      this.setState({
        error: error,
        showSupportError: showSupportError,
        errorInfo: errorInfo,
      });
    }
  }

  render() {
    const { error, errorInfo, showSupportError } = this.state;
    if (errorInfo || showSupportError) {
      // Error path
      return <IntroScreen error={error} showSupportError={showSupportError} />;
    }
    // Normally, just render children
    return this.props.children;
  }
}

export default ErrorBoundary;
