import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from '@material-ui/core/Fab';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components/macro';
import { ColorPalette } from '../../utils/StyleGuide';
import { SYS_ERRORS } from '../../store/errors/enums';

const StyledDialog = styled(props => {
  const { moveLeft, ...rest } = props;
  return <Dialog classes={{ paper: 'paper' }} {...rest} />;
})`
  .paper {
    left: ${props => (props.moveLeft ? '16%' : '')};
  }
`;

const ErrorTitle = styled(({ ...props }) => <Typography {...props} />)`
  font-size: 32px;
  line-height: 1.22;
  text-align: center;
  color: ${ColorPalette.solidRed};
  margin-top: 8px;
`;

const ErrorMessage = styled.div`
  font-size: 18px;
  line-height: 1.22;
  margin-top: ${props => (props.topSpacer ? '22px' : '0')};
  text-align: center;
  color: ${ColorPalette.black};
  margin-bottom: 28px;
  white-space: pre-wrap;
`;

const StyledFab = styled(({ ...props }) => <Fab {...props} />)`
  border-radius: 24px;
  border-width: 3px;
  border-style: solid;
  border-color: #5b5b5b;
  background-color: transparent;
  height: 48px;
  width: auto;
  padding: 0 24px 0 24px;
  align-self: center;
  margin-bottom: 32px;
  box-shadow: none;
`;

const ErrorDialog = ({ open, error, onClose, moveLeft, noTimeout }) => {
  const [clicked, setClicked] = useState(true);
  const showContactMessage = [
    SYS_ERRORS.COULDNT_CHOOSE_GLOBAL_FOCUS_EVALUATOR,
    SYS_ERRORS.SYSTEM_ERROR_OCCURRED,
    SYS_ERRORS.CAMERA_DISCONNECTED,
  ].includes(error.id);

  useEffect(() => {
    let timeoutRef;
    if (error.id && clicked) {
      if (noTimeout) {
        setClicked(false);
      } else {
        timeoutRef = setTimeout(() => setClicked(false), 3000);
      }
    }
    return () => {
      if (!noTimeout) {
        clearTimeout(timeoutRef);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [error.id, error.value, error.cameraId]);
  if (!error.id) {
    return null;
  }

  const displayedMessage = !showContactMessage
    ? error.value
    : `${
        error.value ? `${error.value}\n\n` : ''
      }If you continue to see this message,\nplease contact support`;

  return (
    <StyledDialog
      open={open}
      onClose={
        clicked
          ? null
          : async () => {
              setClicked(true);
              await onClose();
            }
      }
      id="error-dialog"
      moveLeft={moveLeft}
    >
      <DialogTitle disableTypography>
        <ErrorTitle>
          {error.id
            .toLowerCase()
            .split('_')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ')}
        </ErrorTitle>
      </DialogTitle>
      <DialogContent>
        {displayedMessage && displayedMessage !== '' ? (
          <ErrorMessage>{displayedMessage}</ErrorMessage>
        ) : null}
      </DialogContent>
      {error.buttonCaption ? (
        <StyledFab
          id={error.buttonId}
          onClick={
            clicked
              ? null
              : async () => {
                  setClicked(true);
                  await onClose();
                }
          }
          disabled={Boolean(clicked)}
        >
          {error.buttonCaption}
        </StyledFab>
      ) : null}
    </StyledDialog>
  );
};

ErrorDialog.propTypes = {
  open: PropTypes.bool,
  error: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.node.isRequired,
    buttonCaption: PropTypes.string,
  }).isRequired,
  onClose: PropTypes.func.isRequired,
  moveLeft: PropTypes.bool,
  noTimeout: PropTypes.bool,
};

ErrorDialog.defaultProps = {
  moveLeft: false,
  open: true,
  onClose: () => null,
};

export default ErrorDialog;
